import React from 'react';
import { injectIntl } from 'react-intl';
import { Toast } from 'antd-mobile';
import ReactTooltip from 'react-tooltip';
import Tooltip from 'rc-tooltip';
import { APP_STATE } from '@tronlink/lib/constants';
import { PopupAPI } from '@tronlink/lib/api';


const logo = require('@tronlink/popup/src/assets/images/new/logo2.svg');
const justSwap = require('@tronlink/popup/src/assets/images/new/icon-cancel.svg');
const guideTag = 'justswap';


class Header extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            nodeIndex:0,
            //showNodeList:false,
            refresh:false,
            cssPre: {},
            menuList: [],
            guideVisible: false
        }
        this.toggleHover = this.toggleHover.bind(this);
    }

    async componentDidMount() {
        const {menuList = []} = this.props;
        const guideFinished = await PopupAPI.getGuide(guideTag);
        this.setState({ menuList, guideVisible: !guideFinished });
        this.generateStyle();
    }

    async componentWillReceiveProps(nextProps) {
        const {menuList = []} = nextProps;
        this.setState({menuList});
        this.generateStyle();
    }

    toggleHover(id, url){
        const {cssPre} = this.state;
        cssPre[id] = url ? {
            "background-image": `url(${url})`,
            // "background-size": "contain"
        } : null;
        this.setState({cssPre});
    }

    generateStyle(){
        const {cssPre, menuList = []} = this.state;
        Object.prototype.toString.call(menuList).slice(8, -1) === 'Array' &&
        menuList.forEach(groupElement => {
            if (groupElement && groupElement.length && Object.prototype.toString.call(groupElement).slice(8, -1) === 'Array') {
                groupElement.forEach(menu => {
                    const {id, imageUrl} = menu;
                    cssPre[id] = imageUrl ? {
                        "background-image": `url(${imageUrl})`,
                        // "background-size": "contain"
                    } : null;
                })
            }
        });
        this.setState({cssPre});
    }

    generateMenu() {
        const {formatMessage} = this.props.intl;
        const {refresh, cssPre = {}, menuList = [], guideVisible} = this.state;
        let createSettingBar = false;
        let hasSetBar = true;
        const length = menuList.length;

        if (length === 0) {
            createSettingBar = true;
        }
        return <div>
            {
                Object.prototype.toString.call(menuList).slice(8, -1) === 'Array' &&
                menuList.map((groupElement, index) => {
                    if (groupElement && groupElement.length && Object.prototype.toString.call(groupElement).slice(8, -1) === 'Array') {
                        return <div className="linkWrap" style={index === length - 1 ? {"border-right": '0'} : {}}>
                            {
                                index === 0
                                    ?
                                    <Tooltip placement="bottom" overlayClassName="guideCustom" visible={guideVisible} overlay={
                                        <div className="guide">
                                            <div className="content">{formatMessage({ id: 'GUIDE.JUST_SWAP' })}</div>
                                            <img src={justSwap} alt="å–æ¶ˆ" onClick={(e)=>{
                                                e.stopPropagation();
                                                PopupAPI.setGuide(guideTag)
                                                this.setState({guideVisible: false})
                                                PopupAPI.setGaEvent('Home','Click SwapGuide Close','Click SwapGuide Close')
                                            }}/>
                                        </div>
                                    }>
                                        <a href="javascript:void(0)" target="_blank" data-tip={formatMessage({id:'INDEX_ICON_TITLE.JUST_SWAP'})}
                                             data-for="justswap" className={"link link-just " + (guideVisible ? 'link-just-no-hover' : '')}
                                             onClick={(e) => {
                                                 e.stopPropagation();
                                                 PopupAPI.setGaEvent('Home','Click Swap','Click Swap')
                                                 window.open("https://justswap.org/#/home")
                                                 if(guideVisible){
                                                     PopupAPI.setGuide(guideTag)
                                                     this.setState({guideVisible: false})
                                                 }
                                             }}
                                        >&nbsp;</a>
                                    </Tooltip>
                                    : null
                            }
                            {
                                groupElement.map(menu => {
                                    const {id, href, imageUrl, hoveImageUrl} = menu;
                                    switch (id) {
                                        // case 'website':
                                        //     return <a href={href ? href : 'https://www.tronlink.org'} target="_blank"
                                        //               data-tip={formatMessage({id: 'INDEX_ICON_TITLE.OFFICIAL_WEBSITE'})}
                                        //               data-for="website" onClick={() => {
                                        //         PopupAPI.setGaEvent('Home', 'Click Website', 'Click Website')
                                        //     }} className="link link-home" style={cssPre[id]}
                                        //               onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                        //               onMouseLeave={() => this.toggleHover(id, imageUrl)}>
                                        //     </a>;
                                        case 'exchange':
                                            return <a href={href ? href : 'https://trx.market?from=tronlink'}
                                                      target="_blank"
                                                      data-tip={formatMessage({id: 'INDEX_ICON_TITLE.EXCHANGE'})}
                                                      data-for="exchange" className="link link-exchange"
                                                      style={cssPre[id]}
                                                      onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                                      onMouseLeave={() => this.toggleHover(id, imageUrl)}>&nbsp;</a>
                                        case 'ledger':
                                            return <a href="javascript:void(0)"
                                                      data-tip={formatMessage({id: 'CREATION.LEDGER.CONNECT_TITLE'})}
                                                      data-for='ledger' onClick={() => {
                                                PopupAPI.changeState(APP_STATE.LEDGER);
                                                PopupAPI.setGaEvent('Home', 'Click Hardware Wallet', 'Click Hardware Wallet')
                                            }} className="link link-ledger" style={cssPre[id]}
                                                      onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                                      onMouseLeave={() => this.toggleHover(id, imageUrl)}>&nbsp;</a>;
                                        case 'dapp':
                                            return <a href="javascript:void(0)" data-tip='DApp' data-for='dapp'
                                                      onClick={() => {
                                                          PopupAPI.setGaEvent('Dapp List', 'Recommend', 'Recommend')
                                                          PopupAPI.changeState(APP_STATE.DAPP_LIST)
                                                          PopupAPI.setGaEvent('Home', 'Click Dapp', 'Click Dapp')
                                                      }} className="link link-dapp" style={cssPre[id]}
                                                      onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                                      onMouseLeave={() => this.toggleHover(id, imageUrl)}>&nbsp;</a>;
                                        case 'lock':
                                            return <div className="fun"
                                                        data-tip={formatMessage({id: 'INDEX_ICON_TITLE.LOCK'})}
                                                        data-for='lock' onClick={() => {
                                                PopupAPI.lockWallet();
                                                PopupAPI.setGaEvent('Home', 'Click Lock', 'Click Lock')
                                            }} style={cssPre[id]}
                                                        onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                                        onMouseLeave={() => this.toggleHover(id, imageUrl)}>&nbsp;</div>;
                                        case 'refresh':
                                            return <div className="fun"
                                                        data-tip={formatMessage({id: 'INDEX_ICON_TITLE.REFRESH'})}
                                                        data-for='refresh'
                                                        style={cssPre[id]}
                                                        onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                                        onMouseLeave={() => this.toggleHover(id, imageUrl)}
                                                        onClick={() => {
                                                            if (!refresh) {
                                                                this.setState({refresh: true}, async () => {
                                                                    Toast.loading();
                                                                    const r = await PopupAPI.refresh();
                                                                    if (r) {
                                                                        this.setState({refresh: false});
                                                                        Toast.hide();
                                                                    }
                                                                });
                                                            }
                                                            PopupAPI.setGaEvent('Home', 'Click Refresh', 'Click Refresh')
                                                        }}
                                            >&nbsp;</div>;
                                        case 'set':
                                            hasSetBar = false;
                                            return <div className="fun"
                                                        data-tip={formatMessage({id: 'INDEX_ICON_TITLE.SETTING'})}
                                                        data-for='set'
                                                        style={cssPre[id]}
                                                        onMouseOver={() => this.toggleHover(id, hoveImageUrl)}
                                                        onMouseLeave={() => this.toggleHover(id, imageUrl)}
                                                        onClick={() => {
                                                            PopupAPI.changeState(APP_STATE.SETTING);
                                                            PopupAPI.setGaEvent('Home', 'Click Setting', 'Click Setting')
                                                        }}>&nbsp;</div>;
                                    }
                                })
                            }
                            <ReactTooltip id='website' effect='solid' />
                            {guideVisible ? null : <ReactTooltip id='justswap' effect='solid' />}
                            <ReactTooltip id='exchange' effect='solid' />
                            <ReactTooltip id='ledger' effect='solid' />
                            <ReactTooltip id='dapp' effect='solid' />
                            <ReactTooltip id='lock' effect='solid' />
                            <ReactTooltip id='refresh' effect='solid' />
                            <ReactTooltip id='set' effect='solid' />
                        </div>;
                    }
                })
            }
            {
                createSettingBar || hasSetBar ?
                    <div style={{"height":"16px"}}>
                        <div className="fun" data-tip={formatMessage({id: 'INDEX_ICON_TITLE.SETTING'})} data-for='set'
                             onClick={() => {
                                 PopupAPI.changeState(APP_STATE.SETTING);
                                 PopupAPI.setGaEvent('Home', 'Click Setting', 'Click Setting')
                             }}>&nbsp;</div>
                    </div>
                    :
                    null
            }
        </div>
    }

    render() {
        const {
            handleSelectChain,
            showChainList,
            developmentMode,
            chains,
            handleShowChainList
        } = this.props;
        const trxMarketUrl = developmentMode ? 'https://trx.market?from=tronlink' : 'https://trx.market?from=tronlink';
        return (
            <div className='header'>
                <div className='titleContainer'>
                    <div className={'selectedChain '+(chains.selected === '_'?'selected1':'selected2')+(showChainList?' showList':'')} onClick={handleShowChainList}>
                        {/*<img src={logo} alt=""/>*/}
                        <span>{chains.chains[chains.selected].name}</span>
                        <div className='chainWrap' style={showChainList?{height:100}:{height:0}}>
                            {
                                Object.entries(chains.chains).map(([chainId,{name}])=>{
                                   return <div className={'item ' + (chains.selected === chainId ? 'checked' : '')} onClick={(e)=>{
                                       e.stopPropagation();
                                       handleSelectChain(chainId);
                                   }}>
                                        <div className="content">{name}</div>
                                   </div>
                                })
                            }
                        </div>
                    </div>
                    {
                        this.generateMenu()
                    }
                </div>
            </div>
        );
    }
}

export default injectIntl(Header);
