import React from 'react';
import { FormattedMessage, injectIntl } from 'react-intl';
import moment from 'moment';
import { BigNumber } from 'bignumber.js';
import ReactTooltip from 'react-tooltip';
import { Popover } from 'antd-mobile';
import CopyToClipboard from 'react-copy-to-clipboard';
import {PopupAPI} from "@tronlink/lib/api";
import { Toast } from 'antd-mobile';
import { get as lodashGet, toLower as lodashToLower } from 'lodash';

const questionImg = require('@tronlink/popup/src/assets/images/new/icon-question-mark.png');

const myImg = src => <img src={`https://gw.alipayobjects.com/zos/rmsportal/${src}.svg`} className='am-icon am-icon-xs' alt="" />;
class TransactionDetailController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ids: [ 'ownerAddress', 'toAddress', 'hash', 'block' ],
            help: false,
            typeTipShow: false,
            addressBooks: {},
            transactionInfo: {}
        };
        this.copy = this.copy.bind(this);
    }

    async componentDidMount() {
        Toast.loading();
        const hash = lodashGet(this.props, 'selected.transactionDetail.hash');
        const addressBooks = await PopupAPI.getAddressBooks();
        const transactionInfo = await PopupAPI.getTransactionDetail(hash);
        this.setState({ addressBooks, transactionInfo });
        Toast.hide();
    }

    copy(id) {
        const { ids } = this.state;
        const { formatMessage } = this.props.intl;
        ids.forEach(v=>{
            document.getElementById(v).innerText = formatMessage({ id: 'TRANSACTION_DETAIL.ENABLE_COPY' });
        });
        document.getElementById(id).innerText = formatMessage({ id: 'TRANSACTION_DETAIL.HAVE_COPIED' });
    }
    render() {
        const { help, addressBooks, transactionInfo, typeTipShow } = this.state;
        const { selectedToken, selected, onCancel, chains, nodes } = this.props;
        const { formatMessage } = this.props.intl;
        const { transactionDetail: t, address, } = selected;
        // let amount = t.contractType === 31 ? ( t.tokenTransferInfo ? t.tokenTransferInfo.amount_str : t.trigger_info.call_value ) : t.contractData.amount;
        let amount = t.amount;
        amount = amount || 0;
        amount = new BigNumber(amount).shiftedBy(-selectedToken.decimals).toString();
        let tronScanUrl = chains.selected === '_' ? 'https://tronscan.io/#/transaction/' : 'https://dappchain.tronscan.io/#/transaction/'
        if(nodes.selected === '910d7fa5-da35-419d-b454-fd4ee22087cd'){
            tronScanUrl = 'https://nile.tronscan.org/#/transaction/'
        }
        let showAddress = t.toAddress;
        if (addressBooks[showAddress]) {
            const name = addressBooks[showAddress].name;
            showAddress = `${name}(${showAddress})`;
            if (showAddress.length > 34) {
                showAddress = `${showAddress.substr(0, 25)}...${showAddress.substr(-6)}`
            }
        }
        const isFailed = !!(t.contract_ret && lodashToLower(t.contract_ret) !== 'success');
        const { energy_usage = 0, energy_fee = 0, origin_energy_usage = 0, energy_usage_total = 0, net_usage = 0, net_fee = 0 } = transactionInfo.receipt || 0;
        // console.log('transactionInfo:', transactionInfo)
        //event_type show
        let eventTypeShow = false;
        if(t.contractType.toUpperCase() === 'TriggerSmartContract'.toUpperCase() && t.event_type.toUpperCase() === 'Approval'.toUpperCase()){
            eventTypeShow = true;
        }

        return (
            <div className='insetContainer transactions' onClick={ () => { this.setState({ isOpen: { account: false, token: false } }); } }>
                <div className='pageHeader'>
                    <div className='back' onClick={onCancel}> </div>
                    <FormattedMessage id='TRANSACTION_DETAIL'/>
                </div>
                <div className='greyModal detail'>
                    <div className='part1'>
                        <div className={isFailed ? 'iconFailed': 'icon'}> </div>
                        <div className='state'><FormattedMessage id={isFailed ? 'TRANSACTION_DETAIL.TRANSFER_FAIL' : 'TRANSACTION_DETAIL.TRANSFER_SUCCESS'} /></div>
                        <div className='amount'>
                            {t.toAddress === address ? '+' : '-'}{ amount } {selectedToken.abbr}
                        </div>
                    </div>
                    <div className='part2 scroll'>
                        <div className='cell gray' style={{"padding-top": "14px"}}>
                            <div className='title'>
                                <FormattedMessage id='TRANSACTION_DETAIL.SEND_ADDRESS' />
                            </div>
                            <CopyToClipboard text={ t.fromAddress } onCopy={(e) => this.copy('ownerAddress')}>
                                <div className='content'>
                                    <a data-tip={formatMessage({ id: 'TRANSACTION_DETAIL.ENABLE_COPY' })} data-for='ownerAddress'>
                                        { t.fromAddress ? t.fromAddress : '-' }
                                    </a>
                                    <ReactTooltip id='ownerAddress' effect='solid' />
                                </div>
                            </CopyToClipboard>
                        </div>
                        <div className='cell gray'>
                            <div className='title'>
                                 <FormattedMessage id='TRANSACTION_DETAIL.RECEIVE_ADDRESS' />
                            </div>
                            <CopyToClipboard text={ t.toAddress } onCopy={(e) => this.copy('toAddress')}>
                                <div className='content'>
                                    <a data-tip={formatMessage({ id: 'TRANSACTION_DETAIL.ENABLE_COPY' })} data-for='toAddress'>
                                        { showAddress ? showAddress : '-' }
                                    </a>
                                    <ReactTooltip id='toAddress' effect='solid' />
                                </div>
                            </CopyToClipboard>
                        </div>
                        {
                            transactionInfo && transactionInfo.remarks ?
                                <div className='cell gray'>
                                    <div className='title'>
                                        <FormattedMessage id='TRANSACTION_DETAIL.REMARK' />
                                    </div>
                                    <div className='content'>
                                        { transactionInfo.remarks }
                                    </div>
                                </div>
                                : null
                        }
                        <div className='cell gray'> </div>
                        <div className='cell'>
                            <div className='title'>
                                <FormattedMessage id='TRANSACTION_DETAIL.TYPE' />
                            </div>
                            <div className='content'>
                                {
                                    eventTypeShow ?
                                        <div className='typeShow'>
                                            <FormattedMessage id={`CONTRACTS.EVENT_TYPE.${t.event_type.toUpperCase()}`}/>
                                            <Popover
                                                overlayClassName='fortest'
                                                overlayStyle={{ right: "40px", opacity: ".9", color: "#505160" }}
                                                visible={typeTipShow}
                                                overlay={
                                                    <div style={{ padding: 14, background: 'rgba(80,81,96,0.90)', 'border-radius': '4px' }}>
                                                        <div style={{
                                                            'font-family': 'PingFangSC-Regular',
                                                            'font-size': '13px',
                                                            'color': 'hsla(0,0%,100%,.8)',
                                                        }}>
                                                            <FormattedMessage id='TRANSACTION_DETAIL.CONTRACT_TYPE_TIPS'/>
                                                        </div>
                                                    </div>
                                                }
                                                placement='bottomLeft'
                                                align={{
                                                    overflow: { adjustY: 0, adjustX: 0 },
                                                    offset: [-5, 10],
                                                }}
                                            >
                                                <img src={`${questionImg}`} className='am-icon am-icon-xs' alt=""
                                                     onMouseEnter={() => this.setState({ typeTipShow: true })}
                                                     onMouseLeave={() => this.setState({ typeTipShow: false })}/>
                                            </Popover>
                                        </div>
                                        : (t.contractType ? <FormattedMessage id={`CONTRACTS.${t.contractType}`}/> : '')
                                }
                            </div>
                        </div>
                        {
                            eventTypeShow ?
                                <div className='cell'>
                                    <div className='title'>
                                        <FormattedMessage id='TRANSACTION_DETAIL.APPROVAL_AMOUNT' />
                                    </div>
                                    <div className='content'>
                                        { t.approval_amount }
                                    </div>
                                </div>
                                : null
                        }
                        <div className='cell'>
                            <div className='title'>
                                <FormattedMessage id='TRANSACTION_DETAIL.ID' />
                            </div>
                            <CopyToClipboard text={ t.hash } onCopy={(e) => this.copy('hash')}>
                                <div className='content'>
                                    <a data-tip={formatMessage({ id: 'TRANSACTION_DETAIL.ENABLE_COPY' })} data-for='hash'>
                                        { t.hash }
                                    </a>
                                    <ReactTooltip id='hash' effect='solid' />
                                </div>
                            </CopyToClipboard>
                        </div>
                        <div className='cell'>
                            <div className='title'>
                                <FormattedMessage id='TRANSACTION_DETAIL.TIME' />
                            </div>
                            <div className='content'>
                                { moment(t.timestamp).format('YYYY-MM-DD HH:mm:ss')}
                            </div>
                        </div>
                        <div className='cell'>
                            <div className='title'>
                                <FormattedMessage id='TRANSACTION_DETAIL.BLOCK_HEIGHT' />
                            </div>
                            <CopyToClipboard text={ t.block } onCopy={(e) => this.copy('block')}>
                                <div className='content'>
                                    <a data-tip={formatMessage({ id: 'TRANSACTION_DETAIL.ENABLE_COPY' })} data-for='block'>
                                        { t.block }
                                    </a>
                                    <ReactTooltip id='block' effect='solid' />
                                </div>
                            </CopyToClipboard>
                        </div>
                        {
                            <div className='cell'>
                                <div className='title'>
                                    <FormattedMessage id='TRANSACTION_DETAIL.FEE'/>
                                    <Popover
                                        overlayClassName='fortest'
                                        overlayStyle={{ right: "20px", opacity: ".9", color: "#505160" }}
                                        visible={help}
                                        overlay={
                                            <div style={{ padding: 10, background: 'rgba(80,81,96,0.90)' }}>
                                                <div style={{
                                                    'border-bottom': net_fee > 0 || energy_fee > 0 ? 'thin solid #efefef5c' : '',
                                                    'padding-bottom': '10px',
                                                    'margin-bottom': '10px',
                                                    'font-family': 'PingFangSC-Regular',
                                                    'font-size': '13px',
                                                    'color': 'hsla(0,0%,100%,.8)',
                                                }}>
                                                    <FormattedMessage id='TRANSACTION_DETAIL.EXPLAIN.CONSUME'/>
                                                </div>
                                                {
                                                    net_fee > 0?
                                                        <div style={{
                                                            "display":"flex",
                                                            "justify-content": "space-between",
                                                            "align-items": "center",
                                                            "margin-top": "5px",
                                                            'font-size': '13px',
                                                            'color': 'hsla(0,0%,100%,.8)',
                                                        }}>
                                                            <div className="">
                                                                {
                                                                    net_fee === 100000 ?
                                                                        <FormattedMessage id="ACCOUNT.SEND.ACTIVATE_ACCOUNT" />
                                                                        :
                                                                        <FormattedMessage id="ACCOUNT.SEND.BANDWIDTH_NOT_ENOUGH" />
                                                                }
                                                            </div>
                                                            <div>
                                                                {new BigNumber(net_fee).shiftedBy(-6).toString()} TRX
                                                            </div>
                                                        </div>
                                                        :null
                                                }
                                                {
                                                    energy_fee > 0?
                                                        <div style={{
                                                            "display":"flex",
                                                            "justify-content": "space-between",
                                                            "align-items": "center",
                                                            "margin-top": "5px",
                                                            'font-size': '13px',
                                                            'color': 'hsla(0,0%,100%,.8)',
                                                        }}>
                                                            <div className="">
                                                                <FormattedMessage id="ACCOUNT.SEND.ENERGY_NOT_ENOUGH" />
                                                            </div>
                                                            <div>
                                                                {new BigNumber(energy_fee).shiftedBy(-6).toString()} TRX
                                                            </div>
                                                        </div>
                                                        :null
                                                }
                                            </div>
                                        }
                                        placement='right'
                                        align={{
                                            overflow: { adjustY: 0, adjustX: 0 },
                                            offset: [10, 0],
                                        }}
                                    >
                                        <div className='help' onMouseEnter={() => this.setState({ help: true })}
                                             onMouseLeave={() => this.setState({ help: false })}>
                                            {myImg('uQIYTFeRrjPELImDRrPt')}
                                        </div>
                                    </Popover>
                                </div>
                                <div className='content'>
                                    {energy_fee > 0 || net_fee > 0 ? new BigNumber(energy_fee).plus(net_fee).shiftedBy(-6).toString() : "0"} TRX
                                </div>

                            </div>
                        }
                        <div className='cell'>
                            <div className='title'>
                                <FormattedMessage id='TRANSACTION_DETAIL.RESOURCE' />
                            </div>
                            <div className='content'>
                                { new BigNumber(net_fee).dividedBy(10).plus(net_usage).toString() } <FormattedMessage id="TRANSACTIONS.BANDWIDTH"/> &nbsp;&nbsp;&nbsp;
                                { energy_usage_total } <FormattedMessage id="TRANSACTIONS.ENERGY"/>
                            </div>
                        </div>
                        {
                            isFailed ?
                                <div className='cell'>
                                    <div className='title'>
                                        <FormattedMessage id='TRANSACTION_DETAIL.FAILED' />
                                    </div>
                                    <div className='content'>
                                        { t.contract_ret }
                                    </div>
                                </div>
                                : null
                        }
                    </div>
                    <div className='part3' onClick={() => window.open(`${tronScanUrl}${t.hash}`)}>
                        <FormattedMessage id='TRANSACTION_DETAIL.GO_TRONSCAN' />
                    </div>
                </div>
            </div>
        );
    }
}

export default injectIntl(TransactionDetailController);
