import { FormattedHTMLMessage, FormattedMessage, injectIntl } from 'react-intl';
import { PopupAPI } from '@tronlink/lib/api';
import React from 'react';
import './jump.scss';

class Jump extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            counters: 3,
            jumpUrl: ''
        };
    }

    componentDidMount() {
        const { counters = 3, jumpUrl = '' } = this.props;
        this.setState({ counters, jumpUrl });
    }

    async componentWillReceiveProps(nextProps) {
        const { jumpUrl = '' } = nextProps;
        this.setState({jumpUrl})
    }

    counter() {
        let { counters, jumpUrl } = this.state;
        let isJump = false
        if (counters === -1 && !isJump) {
            window.open(jumpUrl);
            isJump = true
        }
        const timer = setInterval(() => {
            if (counters === -2){
                clearInterval(timer)
            }
            this.setState({ counters: --counters });
        }, 1000);
    }

    render() {
        const { showJump, type, id } = this.props;
        const { counters, jumpUrl } = this.state;
        const dom = showJump
            ?
            <div className='popUpConfirm'>
                {this.counter()}
                <div className='jump'>
                    <div className='description'>
                        <FormattedHTMLMessage id={id}/>
                    </div>
                    <div className="buttonRow button">
                        <button className={'customConfirm'}
                                onClick={(e) => {
                                    e.stopPropagation();
                                    window.open(`${jumpUrl}`);
                                    if (type === 'Freeze/Unfreeze') {
                                        PopupAPI.setGaEvent('Home', 'Freeze / Unfreeze Tips Confirm', '');
                                    }
                                }}
                                tabIndex={1}
                        >
                            <FormattedMessage id='BUTTON.CONFIRM_SECONDS' values={{ amount: (counters < 0 ? 0 : counters) || 0 }}/>
                        </button>
                    </div>
                </div>
            </div>
            : null;
        return dom;
    }
}

export default injectIntl(Jump);
