import React from 'react';
import Button from '@tronlink/popup/src/components/Button';

import {FormattedMessage, injectIntl} from 'react-intl';
import './AddressBookDetail.scss';
import {PopupAPI} from "@tronlink/lib/api";
import TronWeb from "tronweb";
import _ from 'lodash';
import {BUTTON_TYPE, VALIDATION_STATE} from "@tronlink/lib/constants";
import {Toast} from "antd-mobile";
import CopyToClipboard from "react-copy-to-clipboard";

import delImg from '../../../assets/images/del.svg';
import delImg2 from '../../../assets/images/new/icon-delete2.svg';


class AddressBookDetail extends React.Component {
    constructor(props) {
        super(props);
        this.onSubmit = this.onSubmit.bind(this);
        this.reset = this.reset.bind(this);
        this.onValueChange = this.onValueChange.bind(this);
        this.onDeleteConfirmed = this.onDeleteConfirmed.bind(this);
        this.onDeleteClose = this.onDeleteClose.bind(this);
        this.state = {
            name: {
                value: '',
                state: VALIDATION_STATE.NONE
            },
            address: {
                value: '',
                state: VALIDATION_STATE.NONE
            },
            remarks: {
                value: '',
                state: VALIDATION_STATE.VALID
            },
            isValid: false,
            addressBooks: {},
            isPage: false,
            showDeleteNodeDialog: false,
            oldAddress: '',
            oldName: '',
            isCopy: false
        }
    }

    async componentDidMount() {
        const {address, isPage = false} = this.props;
        const addressBooks = await PopupAPI.getAddressBooks();
        this.setState({addressBooks, isPage});
        if (address) {
            this.setState({address: {value: address, state: VALIDATION_STATE.VALID}, isCopy:true})
        }
        if (isPage) {
            const selectAddressBook = await PopupAPI.getSelectAddressBook();
            const name = {
                value: selectAddressBook.name,
                state: VALIDATION_STATE.VALID
            };
            const address = {
                value: selectAddressBook.address,
                state: VALIDATION_STATE.VALID
            };
            const remarks = {
                value: selectAddressBook.remarks,
                state: VALIDATION_STATE.VALID
            };
            this.setState({name, address, remarks, isValid: true, oldAddress: selectAddressBook.address, oldName:selectAddressBook.name, isCopy: true});
        }
    }

    async onValueChange(key, value) {
        if (!value.length && key !== 'remarks') {
            if(key === 'address'){
                this.setState({isCopy: false});
            }
            return this.setState({
                [key]: {
                    value: '',
                    state: VALIDATION_STATE.NONE
                }
            });
        }
        const addressBooks = await PopupAPI.getAddressBooks();
        const {name, address, remarks, isPage, oldAddress, oldName} = this.state;
        let nodeState = VALIDATION_STATE.VALID;
        let isValid = false;

        // value = value.trim();
        switch (key) {
            case 'name':
                if (!/^([\u4e00-\u9fa5a-zA-Z0-9\d\s])+$/.test(value)) {
                    nodeState = 'EXCEPTION.ADD_ADDRESS_BOOK.IS_ALPHANUMERIC';
                } else {
                    const index = _(addressBooks).chain().values().findIndex(function (o) {
                        return o.name === value;
                    }).value();
                    if (index !== -1 && oldName !== value) {
                        nodeState = 'EXCEPTION.ADD_ADDRESS_BOOK.IS_UNIQUE2';
                    }
                }
                value = value.slice(0, 20);
                isValid = address.state === VALIDATION_STATE.VALID && remarks.state === VALIDATION_STATE.VALID && nodeState === VALIDATION_STATE.VALID;
                break;
            case 'address':
                value = value.trim();
                this.setState({isCopy: true});
                if (!TronWeb.isAddress(value)) {
                    nodeState = 'EXCEPTION.ADD_ADDRESS_BOOK.ADDRESS_ERROR';
                } else if (addressBooks[value] && oldAddress !== value) { //åœ°å€é‡å¤
                    nodeState = 'EXCEPTION.ADD_ADDRESS_BOOK.IS_UNIQUE';
                }
                isValid = name.state === VALIDATION_STATE.VALID && remarks.state === VALIDATION_STATE.VALID && nodeState === VALIDATION_STATE.VALID;
                break;
            case 'remarks':
                if (value) {
                   if (!/^([\u4e00-\u9fa5a-zA-Z0-9\d\s])+$/.test(value)) {
                        nodeState = 'EXCEPTION.ADD_ADDRESS_BOOK.IS_ALPHANUMERIC';
                    }
                    value = value.slice(0, 60);
                }
                isValid = name.state === VALIDATION_STATE.VALID && address.state === VALIDATION_STATE.VALID && nodeState === VALIDATION_STATE.VALID;
                break;
        }

        this.setState({
            [key]: {
                state: nodeState,
                value
            },
            isValid
        });
    }

    onSubmit() {
        const {formatMessage} = this.props.intl;
        const {name, address, remarks, isPage, oldAddress} = this.state;
        const {
            onConfirmed, onCancel = () => {
            }
        } = this.props;
        const bookInfo = {
            name: name.value,
            remarks: remarks.value,
            address: address.value,
            ts: Date.now()
        };
        this.setState({showAddNodeDialog: false});
        if (isPage) {
            PopupAPI.updateAddressBook(oldAddress ? oldAddress : address.value, address.value, bookInfo);
            onCancel();
            Toast.info(formatMessage({id: 'TOAST.UPDATE'}), 2);
        } else {
            PopupAPI.addAddressBook(address.value, bookInfo);
            onConfirmed(name.value, address.value);
            Toast.info(formatMessage({id: 'TOAST.ADDED'}), 2);
        }
        this.reset();
    }

    onDeleteConfirmed() {
        const {
            onCancel = () => {
            },
        } = this.props;
        const {address} = this.state;
        this.setState({showDeleteNodeDialog: false});
        PopupAPI.deleteAddressBook(address.value);
        onCancel();
    }

    onDeleteClose() {
        this.setState({showDeleteNodeDialog: false});
    }

    reset() {
        this.setState({
            name: {
                value: '',
                state: VALIDATION_STATE.NONE
            },
            address: {
                value: '',
                state: VALIDATION_STATE.NONE
            },
            remarks: {
                value: '',
                state: VALIDATION_STATE.VALID
            },
            isValid: false
        })
    }

    renderDeleteBook() {
        const { showDeleteNodeDialog } = this.state;
        const dom = showDeleteNodeDialog
            ?
            <div className='popUp'>
                <div className='deleteBook'>
                    <div className='title'>
                        <span><FormattedMessage id='SETTING.TITLE.ADDRESS_BOOK.CONFIRM_DELETE' /></span>
                    </div>
                    <div className="img"><img src={delImg2} alt="del"/></div>
                    <div className='txt'>
                        <FormattedMessage id='SETTING.TITLE.ADDRESS_BOOK.CONFIRM_DELETE.BODY' />
                    </div>
                    <div className='buttonRow'>
                        <Button
                            id='BUTTON.CANCEL'
                            type={ BUTTON_TYPE.DANGER }
                            onClick={this.onDeleteClose}
                            tabIndex={ 1 }
                        />
                        <Button
                            id='BUTTON.CONFIRM'
                            onClick={this.onDeleteConfirmed}
                            tabIndex={ 1 }
                        />

                    </div>
                </div>
            </div>
            : null;
        return dom;
    }

    render() {
        const {formatMessage} = this.props.intl;
        const {isValid, name, address, remarks, isPage, showDeleteNodeDialog, isCopy} = this.state;
        const {
            show = true,
            onCancel = () => {
            },
        } = this.props;

        return (
            show ?
                <div className='addAddressWrap'>
                    { showDeleteNodeDialog?
                        <div className="addAddress delDia">
                            {
                                this.renderDeleteBook()
                            }
                        </div>
                        :
                        <div className="addAddress" onClick={(e) => e.stopPropagation()}>
                        {
                            isPage ?
                                <div>

                                    <div className='pageHeader title'>
                                        <div className='back' onClick={() => onCancel()}>&nbsp;</div>
                                        <FormattedMessage id='SETTING.TITLE.ADDRESS_BOOK.DETAIL'/>
                                        <img className="removeItem img" src={delImg} alt="del"
                                             onClick={() => this.setState({showDeleteNodeDialog: true})}
                                        />
                                    </div>
                                </div>
                                :
                                <div className='title'>
                                    <FormattedMessage id='SETTING.TITLE.ADDRESS_BOOK.DETAIL'/>
                                </div>
                        }

                        <label>
                            <FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK.NAME"/>
                        </label>
                        <div className="input">
                            <input type="text" defaultValue={name.value} maxLength="20"
                                // placeholder={formatMessage({id: "SETTINGS.CUSTOM_NODE.NAME.PLACEHOLDER"})}
                                   onChange={(e) => this.onValueChange('name', e.target.value)}/>
                        </div>
                        {
                            !isValid && name.state !== VALIDATION_STATE.VALID && name.state !== VALIDATION_STATE.NONE ?
                                <div className="tipError"><FormattedMessage id={name.state}/></div> : null
                        }
                        <label>
                            <FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK.ADDRESS"/>
                        </label>
                        <div className="input">
                            <div className='showCopy'>
                                <input type="text" defaultValue={address.value}
                                    // placeholder={formatMessage({id: "SETTINGS.CUSTOM_NODE.FULL_NODE.PLACEHOLDER"})}
                                       onChange={e => this.onValueChange('address', e.target.value)}/>
                                {
                                    isCopy ?
                                        <CopyToClipboard text={address.value} onCopy={(e) => {
                                            Toast.info(formatMessage({id: 'TOAST.COPY'}), 2);
                                        }}>
                                            <span className='copy' onClick={() => {
                                                Toast.info(formatMessage({id: 'TOAST.COPY'}), 2)
                                            }}><FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK.COPY"/></span>
                                        </CopyToClipboard>
                                        :
                                        <span className='copy' style={{color: "#bbb", cursor: "not-allowed"}}><FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK.COPY"/></span>
                                }

                            </div>
                        </div>
                        {
                            !isValid && address.state !== VALIDATION_STATE.VALID && address.state !== VALIDATION_STATE.NONE ?
                                <div className="tipError"><FormattedMessage id={address.state}/></div> : null
                        }
                        <label>
                            <FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK.DESC"/>
                        </label>
                        <div className="input">
                            <input type="text" defaultValue={remarks.value} maxLength="60"
                                // placeholder={formatMessage({id: "SETTINGS.TITLE.ADDRESS_BOOK.OPTION"})}
                                   onChange={e => this.onValueChange('remarks', e.target.value)}/>
                        </div>
                        {
                            !isValid && remarks.state !== VALIDATION_STATE.VALID && remarks.state !== VALIDATION_STATE.NONE ?
                                <div className="tipError"><FormattedMessage id={remarks.state}/></div> : null
                        }
                        <Button
                            id='SETTING.TITLE.ADDRESS_BOOK.SAVE'
                            isValid={isValid}
                            onClick={this.onSubmit}
                        />
                        <Button
                            type={BUTTON_TYPE.WHITE}
                            id='SETTING.TITLE.ADDRESS_BOOK.CLOSE'
                            onClick={isPage ? onCancel : this.props.onClose}
                        />
                    </div>
                    }
                </div>
                : null
        );
    }
};
export default injectIntl(AddressBookDetail);

