import React from 'react';
import { PopupAPI } from "@tronlink/lib/api";
import { FormattedMessage, injectIntl } from 'react-intl';
import { APP_STATE } from '@tronlink/lib/constants';
class SettingController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            languages: [
                { name: 'English', key: 'en', selected: true },
                { name: 'ä¸­æ–‡', key: 'zh', selected: false },
                { name: 'æ—¥æœ¬èªž', key: 'ja', selected: false },
            ],
            autoLock: [{
                time: 60 * 1000,
                name: 'SETTING.TITLE.AUTO_LOCK.1_MIN'
            }, {
                time: 5 * 60 * 1000,
                name: 'SETTING.TITLE.AUTO_LOCK.5_MIN'
            }, {
                time: 10 * 60 * 1000,
                name: 'SETTING.TITLE.AUTO_LOCK.10_MIN'
            }, {
                time: 30 * 60 * 1000,
                name: 'SETTING.TITLE.AUTO_LOCK.30_MIN'
            }, {
                time: 0,
                name: 'SETTING.TITLE.AUTO_LOCK.NEVER'
            }]
        };
    }

    setting(index) {
        const options = this.refs.cell.getElementsByClassName('option');
        for(let i = 0;i < options.length;i++) {
            if(i === index) {
                options[ index ].classList.toggle('active');
                if(options[ index ].hasAttribute('data-height')) {
                    const height = options[ index ].getAttribute('data-height');
                    if(options[ index ].classList.contains('active')) {
                        options[ index ].getElementsByClassName('settingWrap')[ 0 ].style.height = height + 'px';
                    } else {
                        options[ index ].getElementsByClassName('settingWrap')[ 0 ].style.height = '0px';
                    }
                }
            }else {
                options[ i ].classList.remove('active');
                if(options[ i ].hasAttribute('data-height')) {
                    options[ i ].getElementsByClassName('settingWrap')[ 0 ].style.height = '0px';
                }
            }
        }
    }


    render() {
        const { prices,onCancel,language,lock,version } = this.props;
        const {languages,autoLock} = this.state;
        return (
            <div className='insetContainer choosingType2'>
                <div className='pageHeader'>
                    <div className="back" onClick={ onCancel }>&nbsp;</div>
                    <FormattedMessage id="SETTING.TITLE" />
                </div>
                <div className='greyModal' ref="cell">
                    <div className="optionsWrap">
                        <div className="option" onClick={ ()=>PopupAPI.changeState(APP_STATE.ADDRESS_BOOK) }>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK" />
                                </div>
                            </div>
                        </div>
                        <div className="option" onClick={ ()=>{
                            PopupAPI.changeState(APP_STATE.DAPP_WHITELIST);
                            PopupAPI.setGaEvent('Setting','Click DApp White List','Click DApp White List')
                        }}>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.DAPP_WHITELIST" />
                                </div>
                            </div>
                        </div>
                        <div className="option" onClick={ ()=>{
                            PopupAPI.changeState(APP_STATE.NODE_MANAGE);
                            PopupAPI.setGaEvent('Setting','Click Node Management','Click Node Management')
                        } }>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.NODE_MANAGE" />
                                </div>
                            </div>
                        </div>
                        <div className="option" onClick={ ()=>{
                            this.setting(3);
                            PopupAPI.setGaEvent('Setting','Click Currency','Click Currency')
                        } }>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.CURRENCY" />
                                    <div className="unit">{prices.selected}</div>
                                </div>
                                <div className="settingWrap">
                                    {
                                        Object.entries(prices.priceList).filter(([key,val])=>key !== 'USDT'&& typeof val !== 'object').map(([key,val])=><div key={key} onClick={(e)=>{
                                            e.stopPropagation();
                                            PopupAPI.selectCurrency(key);
                                            PopupAPI.setGaEvent('Currency','Switch Currency',`${key}`)
                                        }} className={"unit"+(key === prices.selected?" selected":"")}>{key} ({val})</div>)
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="option" onClick={ ()=>{
                            this.setting(4)
                            PopupAPI.setGaEvent('Setting','Click Language','Click Language')
                        }}>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.LANGUAGE" />
                                    <div className="unit">
                                        {
                                            languages.filter(({key})=>key === language)[0].name
                                        }
                                    </div>
                                </div>
                                <div className="settingWrap">
                                    {
                                        languages.map(({name,selected,key})=><div key={name} onClick={(e)=>{
                                            e.stopPropagation();
                                            PopupAPI.setLanguage(key);
                                            if(key==='en'){
                                                PopupAPI.setGaEvent('Language','Switch to English','Switch to English')
                                            } else if(key === 'zh'){
                                                PopupAPI.setGaEvent('Language','Switch to Chinese','Switch to Chinese')
                                            }
                                        }} className={"unit"+(key === language?" selected":"")}>{name}</div>)
                                    }
                                </div>
                            </div>
                        </div>
                        <div className="option" onClick={() =>{
                            this.setting(5)
                            PopupAPI.setGaEvent('Setting','Click Auto Lock','Click Auto Lock')
                        }   }>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.AUTO_LOCK" />
                                    <div className="unit">
                                        <FormattedMessage id={autoLock.filter(({time})=>time === lock.duration)[0].name} />
                                    </div>
                                </div>
                                <div className="settingWrap">
                                    {
                                        autoLock.map(({name,time})=>(
                                            <div key={time} onClick={async (e)=>{
                                                e.stopPropagation();
                                                let setting = await PopupAPI.getSetting();
                                                setting.lock={lockTime:new Date().getTime(),duration:time};
                                                PopupAPI.setSetting(setting);
                                                if (name === 'SETTING.TITLE.AUTO_LOCK.10_MIN') {
                                                    PopupAPI.setGaEvent('Auto Lock','Set 10min','Set 10min')
                                                }else if(name ==='SETTING.TITLE.AUTO_LOCK.1_MIN'){
                                                    PopupAPI.setGaEvent('Auto Lock','Auto Lock','1 min')
                                                }else if(name === 'SETTING.TITLE.AUTO_LOCK.30_MIN'){
                                                    PopupAPI.setGaEvent('Auto Lock','Auto Lock','30 min')
                                                }else if(name === 'SETTING.TITLE.AUTO_LOCK.NEVER'){
                                                    PopupAPI.setGaEvent('Auto Lock','Auto Lock','Never')
                                                }else if(name === "SETTING.TITLE.AUTO_LOCK.5_MIN"){
                                                    PopupAPI.setGaEvent('Auto Lock','Auto Lock','5 min')
                                                }
                                            }} className={"unit"+(time === lock.duration ? " selected":"")}>
                                                <FormattedMessage id={name} />
                                            </div>
                                        ))
                                    }
                                </div>
                            </div>

                        </div>
                        <div className="option" onClick={() =>{
                            PopupAPI.setGaEvent('Setting','Click About','Click About')
                            window.open('https://www.tronlink.org')
                        }   }>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.ABOUT" />
                                </div>
                            </div>
                        </div>
                        <div className="option" onClick={() =>{
                            PopupAPI.lockWallet()
                            PopupAPI.setGaEvent('Setting','Log Out','Log Out')
                        }   }>
                            <div className="txt">
                                <div className="span">
                                    <FormattedMessage id="SETTING.TITLE.LOCK" />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="version">
                        V{version}
                        {/*<FormattedMessage id="COMMON.CURRENT_VERSION" values={{version}} />*/}
                    </div>
                </div>
            </div>
        );
    }

};

export default injectIntl(SettingController);
