import React from 'react';
import {FormattedHTMLMessage, FormattedMessage, injectIntl} from 'react-intl';
import {PopupAPI} from '@tronlink/lib/api';
import ProcessBar from "../components/ProcessBar";
import {connect} from "react-redux";
import Button from "../components/Button";
import {BUTTON_TYPE} from "@tronlink/lib/constants";

class ResourceDetailController extends React.Component {
    constructor() {
        super();
        this.state = {
            showJump: false,
            counters: 3,
            jumpUrl: 'https://tronscan.io/#'
        };
    }

    async componentDidMount() {
        const {chains} = this.props;
        const tronscanUrl = 'https://tronscan.io/#';
        const dappChainUrl = 'https://dappchain.tronscan.io/#';
        let jumpUrl = tronscanUrl;
        if (chains.selected !== '_') {
            jumpUrl = dappChainUrl
        }
        this.setState({jumpUrl})
    }

    renderJump() {
        const {showJump, counters, jumpUrl} = this.state;
        const dom = showJump
            ?
            <div className='popUp'>
                {this.counter()}
                <div className='jump'>
                    <div className='description'>
                        <FormattedHTMLMessage id='ACCOUNT.RESOURCE_DETAIL.JUMP_DESC'/>
                    </div>
                    <div className="buttonRow button">
                        <button className={'customButton ' + BUTTON_TYPE.PRIMARY + ' is-valid'}
                                onClick={(e) => {
                                    e.stopPropagation();
                                    window.open(`${jumpUrl}/account?from=tronlink&type=frozen`);
                                    PopupAPI.setGaEvent('Home','Freeze / Unfreeze Tips Confirm','')
                                }}
                                tabIndex={1}
                        >
                            <FormattedMessage id='BUTTON.CONFIRM_SECONDS' values={{amount: counters || 0}}/>
                        </button>
                    </div>
                </div>
            </div>
            : null;
        return dom;
    }

    counter() {
        let {counters, jumpUrl} = this.state;
        if (counters < 0) {
            window.open(`${jumpUrl}/account?from=tronlink&type=frozen`);
        }
        setInterval(() => {
            this.setState({counters: --counters})
        }, 1000)
    }

    render() {
        const {onCancel, accounts, nodes} = this.props;
        const {formatMessage} = this.props.intl;
        const account = accounts.accounts[accounts.selected.address];

        return (

            account ?
                <div className='insetContainer resources'>
                    {this.renderJump()}
                    <div className='pageHeader'>
                        <div className='back' onClick={onCancel}></div>
                        <span className='title'>{
                            formatMessage({id: 'ACCOUNT.RESOURCE_DETAIL.TITLE'})
                        }</span>
                    </div>
                    <div className='greyModal'>
                        <div className='cell'>
                            <div className='title'>
                                <FormattedMessage id='CONFIRMATIONS.RESOURCE.BANDWIDTH'/>
                                <div className='num'>
                                    <span className="usedResource">{account.netLimit - account.netUsed}</span>
                                    <span className="allResource">&nbsp;/&nbsp;{account.netLimit}</span>
                                </div>
                            </div>
                            <div className="showBar">
                                <ProcessBar percentage={(account.netLimit - account.netUsed) / account.netLimit}
                                            len={99}/>
                            </div>
                            <div className="description">
                                {formatMessage({id: "ACCOUNT.RESOURCE_DETAIL.BANDWIDTH.DESC"})}
                            </div>
                            <div className="buttonRow">
                                <button
                                    onClick={() => {
                                        this.setState({showJump: true});
                                        PopupAPI.setGaEvent('Home','Click Freeze / Unfreeze','Bandwidth')
                                    }}
                                >
                                    <FormattedMessage id={"MENU.FROZEN_UNFROZEN"}/>
                                </button>
                            </div>
                        </div>
                        <div className="cell">
                            <div className='title'>
                                <FormattedMessage id='CONFIRMATIONS.RESOURCE.ENERGY'/>
                                <div className='num'>
                                    <span
                                        className="usedResource">{(account.energy - account.energyUsed) < 0 ? 0 : (account.energy - account.energyUsed)}</span>
                                    <span
                                        className="allResource">&nbsp;/&nbsp;{account.energy < 0 ? 0 : account.energy}</span>
                                </div>
                            </div>
                            <div className="showBar">
                                <ProcessBar
                                    percentage={(account.energy <= 0 || (account.energy - account.energyUsed) < 0) ? 0 : (account.energy - account.energyUsed) / account.energy}
                                    len={99}
                                />
                            </div>
                            <div className="description">
                                {formatMessage({id: "ACCOUNT.RESOURCE_DETAIL.ENERGY.DESC"})}
                            </div>
                            <div className="buttonRow">
                                <button
                                    onClick={() => {
                                        this.setState({showJump: true});
                                        PopupAPI.setGaEvent('Home','Click Freeze / Unfreeze','Energy')
                                    }}
                                >
                                    <FormattedMessage id={"MENU.FROZEN_UNFROZEN"}/>
                                </button>
                                <button
                                    onClick={() => {
                                        if (nodes.selected === '109c64ad-e59c-46fe-ba87-179587e6c772' || nodes.selected === '8eeb5be6-5e10-4283-ae61-03c0e4726fe0')
                                            window.open('http://www.tronlending.org');
                                        PopupAPI.setGaEvent('Home','Click TronLending','Click TronLending')
                                    }}
                                >
                                    <FormattedMessage id={"ACCOUNT.RESOURCE_DETAIL.ENERGY.BORROW"}/>
                                </button>
                            </div>

                        </div>
                    </div>
                </div>
                :
                null
        );
    }
}

export default injectIntl(
    connect(state => ({
        accounts: state.accounts,
        nodes: state.app.nodes,
        chains: state.app.chains,
    }))(ResourceDetailController)
);
