import React from 'react';
import moment from 'moment';
import CopyToClipboard from 'react-copy-to-clipboard';
import { Toast } from 'antd-mobile';
import { BigNumber } from 'bignumber.js';
import { FormattedMessage, injectIntl } from 'react-intl';
import { PopupAPI } from '@tronlink/lib/api';
import { APP_STATE, CONTRACT_ADDRESS, ACCOUNT_TYPE, JUST_SWAP_URL, JUST_SWAP_URL_EN, POLONI_DEX_URL } from '@tronlink/lib/constants';
import Tooltip from 'rc-tooltip';
import {
    floor as lodashFloor, size as lodashSize, toLower as lodashToLower, filter as lodashFilter,
    keyBy as lodashKeyBy, get as lodashGet, find as lodashFind, orderBy as lodashOrderBy, reject as lodashReject
} from 'lodash';
import 'rc-tooltip/assets/bootstrap.css';

BigNumber.config({ EXPONENTIAL_AT: [-20, 30] });
const token10DefaultImg = require('@tronlink/popup/src/assets/images/new/token_10_default.png');

class TransactionsController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            index: 0,
            isTop: false,
            transactions: {
                records: [],
                total: 0
            },
            isRequest: false,
            currentPage: 1,
            addressBooks: {},
            hostname: 'https://tronscan.io/#',
            showMoreMenu: false,
            tokenInMarket: {},
            localRecords: []
        };
    }

    async componentDidMount() {

        const {
            accounts,
            nodes
        } = this.props;
        const { id = '_' } = accounts.selectedToken;
        Toast.loading('', 0, false, false);
        const transactions = await PopupAPI.getTransactionsByTokenId(id);
        const addressBooks = await PopupAPI.getAddressBooks();
        const tokenInMarket = await PopupAPI.getTokenInMarket(id === '_' ? 'T9yD14Nj9j7xAB4dbGeiX9h8unkKHxuWwb' : id);
        // local transactions
        const allRecords = await PopupAPI.getLocalRecord();
        const localRecords = lodashFilter(allRecords, o => {
            return o.tokenId === id;
        });
        // console.log("localRecords",JSON.stringify(localRecords))

        this.setState({ transactions, addressBooks, tokenInMarket, localRecords, hostname: nodes.nodes[nodes.selected].hostname });
        Toast.hide();
        this.combineData();
        this.checkLocalRecord();
    }

    combineData() {
        let { localRecords, transactions: { records, total, finger } } = this.state;
        const now = Date.now();
        for (let index = 0; index < localRecords.length; index++) {
            const localInfo = localRecords[index];
            const existInfo = lodashFind(records, o => {
                return o.hash === localInfo.txId;
            });
            if (((localInfo.timestamp || 0) + 300000) < now || existInfo) { // timeout five minutes
                // console.log('combineData delete:', localInfo);
                localRecords.splice(index, 1);
                PopupAPI.deleteLocalRecord(localInfo.txId);
                continue;
            }
            // console.log('push:', localInfo);
            records.push(localInfo);
        }
        records = lodashOrderBy(records, ['timestamp'], ['desc']);
        this.setState({ localRecords: localRecords, transactions: { records: records, total: total, finger } });
    }

    async checkLocalRecord() {
        const { accounts } = this.props;
        const { id = '_' } = accounts.selectedToken;
        let { localRecords, transactions: { records, total, finger } } = this.state;
        if (lodashSize(localRecords) > 0) {
            const transactions = await PopupAPI.getTransactionsByTokenId(id);
            const transactionInfos = lodashKeyBy(transactions.records || [], 'hash');
            const now = Date.now();
            for (let index = 0; index < localRecords.length; index++) {
                const localInfo = localRecords[index];
                if ((localInfo.timestamp || 0) + 300000 < now) { // timeout five minutes
                    localRecords.splice(index, 1);
                    PopupAPI.deleteLocalRecord(localInfo.txId);
                    // console.log('checkLocalRecord delete:', localInfo);
                    records = lodashReject(records, o => {
                        return o.txId === localInfo.txId
                    });
                    continue;
                }
                if (lodashGet(transactionInfos, localInfo.txId)) {// update current list
                    localRecords.splice(index, 1);
                    PopupAPI.deleteLocalRecord(localInfo.txId);
                    // console.log('checkLocalRecord 1212 delete:', localInfo);
                    let existInfo = lodashFind(records, o => {
                        return o.txId = localInfo.txId;
                    });
                    Object.assign(existInfo, { local: false }, lodashGet(transactionInfos, localInfo.txId));
                    // console.log('update:', localInfo, existInfo);
                }
            }
            this.setState({ localRecords: localRecords, transactions: { records: records, total: total, finger } });
            if (lodashSize(localRecords) > 0) {
                setTimeout(() => {
                    this.checkLocalRecord();
                }, 5000);
            }
        }
    }

    render() {
        let { transactions } = this.state;
        const { index, isTop, isRequest, currentPage, addressBooks, hostname, showMoreMenu, tokenInMarket } = this.state;
        const {
            accounts,
            onCancel,
            prices,
            chains,
            nodes,
            language,
        } = this.props;

        const { formatMessage } = this.props.intl;
        const { address, airdropInfo, type } = accounts.selected;
        let { id = '_', name = 'TRX', decimals = 6, imgUrl, price = 0, amount, balance = 0, frozenBalance = 0 } = accounts.selectedToken;
        if(id === '_'){
            amount = new BigNumber(balance).plus(frozenBalance).toString()
        }
        return (
            <div className='insetContainer transactions'>
                <div className='pageHeader'>
                    <div className='back' onClick={
                        () => {
                            Toast.hide();
                            onCancel();
                        }}></div>
                    <span className='title'>{name}</span>
                    {
                        id !== '_' ?
                            <span className='detail' onClick={() => {
                                let url = hostname;
                                url += (id.match(/^T/) ? '/token20/' + id : '/token/' + id);
                                window.open(url);
                            }
                            }
                            >
                                <FormattedMessage id='TRANSACTION.TOKEN_INFO.DETAIL'/>
                            </span>
                            : null
                    }

                </div>
                <div className='greyModal' onClick={(e)=>{
                    e.stopPropagation()
                    this.setState({showMoreMenu: false})
                }}>
                    <div className='showTokenInfo' style={isTop ? {
                        height: 0,
                        paddingTop: 0,
                        overflow: 'hidden'
                    } : {
                        overflow: id === CONTRACT_ADDRESS.USDT ? 'visible' : 'hidden',
                        height: (id === '_' || (id === CONTRACT_ADDRESS.USDT && airdropInfo.isShow) ? 216 : 'auto')
                    }}>
                        <img src={imgUrl ? imgUrl : token10DefaultImg}
                             onError={(e) => e.target.src = token10DefaultImg}/>
                        <div className='amount'>
                            {amount}
                        </div>
                        {(id === '_' || id === CONTRACT_ADDRESS.USDT ? lodashFloor(price * amount, 3) > 0 : lodashFloor(price * amount * prices.priceList[prices.selected], 3) > 0) &&
                        <div className='worth'>
                            â‰ˆ {id === '_' || id === CONTRACT_ADDRESS.USDT ? lodashFloor(price * amount, 3) : lodashFloor(price * amount * prices.priceList[prices.selected], 3)} {prices.selected}
                        </div>
                        }
                        {
                            id === '_' ?
                                <div className='desc trx'>
                                    <div className='cell'>
                                        <div className='row1'>
                                            {balance}
                                        </div>
                                        <div className='row2'>
                                            <FormattedMessage id='TRANSACTION.TOKEN_INFO.AVAILABLE_BALANCE'/>
                                        </div>
                                    </div>
                                    <div className='cell'>
                                        <div className='row1'>
                                            {frozenBalance}
                                        </div>
                                        <div className='row2'>
                                            <FormattedMessage id='TRANSACTION.TOKEN_INFO.FROZEN_BALANCE'/>
                                        </div>
                                    </div>
                                </div>
                                :
                                (
                                    id.match(/^T/)
                                        ?
                                        (
                                            id === CONTRACT_ADDRESS.USDT && airdropInfo.isShow ?
                                                <div className='desc usdt'>
                                                    <div className='usdt_inner'
                                                         onClick={() => PopupAPI.changeState(APP_STATE.USDT_INCOME_RECORD)}>
                                                        <div className='usdt_inner_bg'>
                                                            <div className='cell'>
                                                                <div className='income'>
                                                                    <div className='txt'>
                                                                        <FormattedMessage
                                                                            id='USDT.TEXT.YESTERDAY_INCOME'/>
                                                                    </div>
                                                                    <div className='number'>
                                                                        +{new BigNumber(new BigNumber(airdropInfo.yesterdayEarnings).shiftedBy(-6).toFixed(2)).toFormat()}
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className='cell'>
                                                                <div className='income'>
                                                                    <div className='txt'>
                                                                        <FormattedMessage id='USDT.TEXT.TOTAL_INCOME'/>
                                                                    </div>
                                                                    <div className='number'>
                                                                        +{new BigNumber(new BigNumber(airdropInfo.totalEarnings).shiftedBy(-6).toFixed(2)).toFormat()}
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                :
                                                <div></div>
                                        )
                                        :
                                        <div className='desc token'>
                                            ID:&nbsp;{id}
                                            <CopyToClipboard text={id} onCopy={() => {
                                                Toast.info(formatMessage({ id: 'TOAST.COPY' }));
                                            }}>
                                                <span className='copy'>&nbsp;</span>
                                            </CopyToClipboard>
                                        </div>
                                )

                        }

                    </div>
                    <div className='tabNav'>
                        <div className={index == 0 ? 'active' : ''} onClick={async () => {
                            this.setState({ index: 0 });
                            Toast.loading('', 0);
                            const transactions = await PopupAPI.getTransactionsByTokenId(id, '', 'all');
                            Toast.hide();
                            this.setState({ transactions, currentPage: 1, isRequest: false });

                        }}>
                            <FormattedMessage id='ACCOUNT.ALL'/>
                        </div>
                        <div className={index == 2 ? 'active' : ''} onClick={async () => {
                            this.setState({ index: 2 });
                            Toast.loading('', 0);
                            const transactions = await PopupAPI.getTransactionsByTokenId(id, '', 'from');
                            Toast.hide();
                            this.setState({ transactions, currentPage: 1, isRequest: false });

                        }}>
                            <FormattedMessage id='ACCOUNT.RECEIVE'/>
                        </div>
                        <div className={index === 1 ? 'active' : ''} onClick={async () => {
                            this.setState({ index: 1 });
                            Toast.loading('', 0);
                            const transactions = await PopupAPI.getTransactionsByTokenId(id, '', 'to');
                            Toast.hide();
                            this.setState({ transactions, currentPage: 1, isRequest: false });
                        }}>
                            <FormattedMessage id='ACCOUNT.SEND'/>
                        </div>
                    </div>
                    <div className='transaction scroll' onScroll={async (e) => {
                        const key = index === 0 ? 'all' : (index === 1 ? 'to' : 'from');
                        if (transactions.records.length > 8) {
                            const isTop = e.target.scrollTop === 0 ? false : true;
                            this.setState({ isTop });
                            if (e.target.scrollTop === ((58 * transactions.records.length + 36) - 484)) {
                                if (!isRequest) {
                                    this.setState({ isRequest: true });
                                    Toast.loading('', 0);
                                    const records = await PopupAPI.getTransactionsByTokenId(id, typeof transactions.finger === 'string' ? transactions.finger : ++transactions.finger, key);
                                    Toast.hide();
                                    if (records.records.length === 0 || !records.finger) {
                                        this.setState({ isRequest: true });
                                    } else {
                                        transactions.records = transactions.records.concat(records.records);
                                        transactions.finger = records.finger;
                                        this.setState({ transactions, isRequest: false });
                                    }
                                }
                            }
                        }
                    }}
                    >
                        {
                            transactions.records.length > 0 ?
                                <div className='lists'>
                                    {
                                        transactions.records.map((v, transIndex) => {

                                            const direction = v.toAddress === v.fromAddress ? 'send' : (v.toAddress === address ? 'receive' : 'send');
                                            const addr = v.toAddress === address ? v.fromAddress : v.toAddress;

                                            let showAddress = addr;
                                            if (addressBooks[showAddress]) {
                                                const name = addressBooks[showAddress].name;
                                                showAddress = `...${showAddress.substr(-5)}`
                                                showAddress = `${name}(${showAddress})`;
                                                // if (showAddress.length > 30) {
                                                //     showAddress = `${showAddress.substr(0, 24)}...${showAddress.substr(-6)}`
                                                // }
                                            }else{
                                                showAddress = `${showAddress.substr(0, 5)}...${showAddress.substr(-5)}`;
                                            }
                                            const isFailed = !!(v.contract_ret && lodashToLower(v.contract_ret) !== 'success');
                                            return (
                                                <div className={`item ${direction}`} key={transIndex}
                                                     onClick={async (e) => {
                                                         e.stopPropagation();
                                                         if(v.local){
                                                             Toast.info(formatMessage({ id: 'TRANSACTIONS.CONFIRM.DESC' }), 2);
                                                             return;
                                                         }
                                                         // if(v.confirmed === 1 || moment().isAfter(v.timestamp, 'minute')) {
                                                         Toast.loading('', 0);
                                                         await PopupAPI.setTransactionDetail(v);
                                                         Toast.hide();
                                                         PopupAPI.changeState(APP_STATE.TRANSACTION_DETAIL);
                                                         // }else{
                                                         //     Toast.info(formatMessage({ id: 'TRANSACTIONS.CONFIRM.DESC' }), 2);
                                                         // }
                                                     }}>
                                                    <div className='left'>
                                                        <div
                                                            className='address'>{
                                                            addr ? showAddress
                                                                    : (v.contractType !== 'TransferContract' ? <FormattedMessage id='TRANSACTIONS.SYSTEM_CONTRACT'/> : '-')
                                                            }</div>
                                                        <div
                                                            className='time'>{moment(v.timestamp).format('YYYY-MM-DD HH:mm:ss')}</div>
                                                    </div>
                                                    <div className={'right ' + (isFailed ? "failed" : (v.local ? 'unConfirmed' : ''))}>
                                                        <div className="amount">{v.local ? v.amount : lodashFloor(new BigNumber(v.amount).shiftedBy(-decimals).toString(), 6)}</div>
                                                        {
                                                            isFailed ? <div className="state"><FormattedMessage id='TRANSACTIONS.FAILED'/></div>
                                                                : <div> </div>
                                                        }
                                                        {
                                                            v.local ? <div className="state"><FormattedMessage id='TRANSACTIONS.CONFIRMING'/></div>
                                                                : <div> </div>
                                                        }

                                                    </div>
                                                </div>
                                            );
                                        })
                                    }
                                </div>
                                :
                                <div className='noData'>
                                    <FormattedMessage id='TRANSACTIONS.NO_DATA'/>
                                </div>
                        }
                    </div>
                </div>
                <div className='buttonGroup'>
                    <button className='receive' onClick={(e) => {
                        PopupAPI.changeDealCurrencyPage(1);
                        PopupAPI.changeState(APP_STATE.RECEIVE);
                        PopupAPI.setGaEvent('Token','Click Receive',`${name}(${id})`)
                    }}
                    >
                        <FormattedMessage id='ACCOUNT.RECEIVE'/>
                    </button>
                    <div className="line">&nbsp;</div>
                    <button className='send' onClick={(e) => {
                        PopupAPI.changeDealCurrencyPage(1);
                        PopupAPI.changeState(APP_STATE.SEND);
                    }}>
                        <FormattedMessage id='ACCOUNT.SEND'/>
                    </button>
                    {
                        (accounts.selectedToken.isMapping && type !== ACCOUNT_TYPE.LEDGER) ||  lodashSize(tokenInMarket) > 0?
                            <div className="line">&nbsp;</div>
                            :
                            null
                    }
                    {
                        lodashSize(tokenInMarket) > 0 || (accounts.selectedToken.isMapping && type !== ACCOUNT_TYPE.LEDGER && nodes.nodes[nodes.selected].connect) ?
                            // <Tooltip placement="topRight" overlay={formatMessage({ id: 'ACCOUNT.TRANSFER.HOVER' + (chains.selected === '_' ? '' : '2') })}>
                            //     <button className='transfer' onClick={(e) => {
                            //         PopupAPI.changeState(APP_STATE.TRANSFER);
                            //     }} >
                            //         <FormattedMessage id={'ACCOUNT.TRANSFER' + (chains.selected === '_' ? '' : '2')}/>
                            //     </button>
                            // </Tooltip>
                            <Tooltip placement="topRight" overlayClassName="transactionCustom" visible={showMoreMenu} overlay={
                                <div className='content'>
                                    {
                                        chains.selected === '_' && (tokenInMarket.justSwap) ?
                                            <div className="detail justswap" onClick={(e)=>{
                                                e.stopPropagation()
                                                let url = language === 'zh'? JUST_SWAP_URL: JUST_SWAP_URL_EN;
                                                if(id && id !== '_'){
                                                    url += `?tokenAddress=${id}&type=swap`
                                                }
                                                window.open(url)
                                                PopupAPI.setGaEvent('Token','Click Swap',`${name}(${id})`)
                                            }}>
                                                <div>
                                                    <div className="title"><FormattedMessage id={'ACCOUNT.MORE.JUSTSWAP'}/></div>
                                                    <div className="tip"><FormattedMessage id={'ACCOUNT.MORE.JUSTSWAP.TIPS'}/></div>
                                                </div>
                                            </div>
                                            : null
                                    }
                                    {
                                        chains.selected === '_' && tokenInMarket.market ?
                                            <div className="detail poloniex" onClick={e => {
                                                e.stopPropagation()
                                                window.open(POLONI_DEX_URL + `?id=${tokenInMarket.market}`)
                                                PopupAPI.setGaEvent('Token','Click Exchange',`${name}(${id})`)
                                            }}>
                                                <div>
                                                    <div className="title"> <FormattedMessage id={"ACCOUNT.MORE.MARKET"}/> </div>
                                                    <div className="tip"> <FormattedMessage id={"ACCOUNT.MORE.MARKET.TIPS"}/> </div>
                                                </div>
                                            </div>
                                            : null
                                    }
                                    {
                                        accounts.selectedToken.isMapping && type !== ACCOUNT_TYPE.LEDGER && nodes.nodes[nodes.selected].connect ?
                                            <div className="detail transfer" onClick={(e) => {
                                                e.stopPropagation();
                                                PopupAPI.changeState(APP_STATE.TRANSFER);
                                                if(chains.selected === '_'){
                                                    PopupAPI.setGaEvent('Token','Click toDAppChain',`${name}(${id})`)
                                                } else {
                                                    PopupAPI.setGaEvent('Token','Click toMainChain',`${name}(${id})`)
                                                }
                                            }}>
                                                <div>
                                                    <div className="title"><FormattedMessage id={'ACCOUNT.TRANSFER' + (chains.selected === '_' ? '' : '2')}/></div>
                                                    <div className="tip"><FormattedMessage id={'ACCOUNT.TRANSFER.HOVER' + (chains.selected === '_' ? '' : '2')}/></div>
                                                </div>
                                            </div>
                                            :
                                            null
                                    }
                                </div>
                            } align={{
                                overflow: { adjustY: 0, adjustX: 0 },
                                offset: [0, -13],
                            }} >
                                <button className={'more ' + (showMoreMenu ? 'arrow-up' : '')} onClick={(e) => {
                                    this.setState({showMoreMenu: !showMoreMenu})
                                    if(!showMoreMenu){
                                        PopupAPI.setGaEvent('Token','Click More',`${name}(${id})`)
                                    }
                                }} >
                                    <FormattedMessage id={'ACCOUNT.MORE'}/>
                                </button>
                            </Tooltip>
                            :
                            null
                    }

                </div>
            </div>
        );
    }
}

export default injectIntl(TransactionsController);
