import React from 'react';
import Button from '@tronlink/popup/src/components/Button';
import TronWeb from 'tronweb';
import Dropdown from 'react-dropdown';
import Utils from '@tronlink/lib/utils';
import { PopupAPI } from '@tronlink/lib/api';
import { connect } from 'react-redux';
import {Switch} from 'antd-mobile'
import ReactTooltip from 'react-tooltip';
import {
    FormattedMessage,
    FormattedHTMLMessage,
    injectIntl
} from 'react-intl';

import {
    CONFIRMATION_TYPE,
    BUTTON_TYPE,
    ACCOUNT_TYPE
} from '@tronlink/lib/constants';
import {get as lodashGet, size as lodashSize} from 'lodash';

import 'react-dropdown/style.css';
import './ConfirmationController.scss';
import Loading from "../../components/Loading";
import {Toast} from "antd-mobile";
import BigNumber from 'bignumber.js';
import { app } from '../../index';
const questionImg = require('@tronlink/popup/src/assets/images/new/icon-question-mark.svg');

class ConfirmationController extends React.Component {
    constructor({ intl }) {
        super();
        this.loadWhitelistOptions(intl);
        this.onReject = this.onReject.bind(this);
        this.onAccept = this.onAccept.bind(this);
        this.onWhitelist = this.onWhitelist.bind(this);
        this.onAcceptLedger = this.onAcceptLedger.bind(this);
        this.tokensMap = JSON.parse(localStorage.getItem('tokensMap'));
        this.listener = this.listener.bind(this);
    }

    async componentDidMount() {
        window.resizeTo(360 + window.outerWidth - window.innerWidth , 600 + window.outerHeight - window.innerHeight);
        const {
            type,
            contractType,
            input: { parameter, contract_address, function_selector, token_id, call_token_value, call_value, owner_address }
        } = this.props.confirmation;
        window.addEventListener('message',this.listener,false);

        // need try catch
        if (contractType === 'TriggerSmartContract') {
            try{
                if (type === CONFIRMATION_TYPE.TRANSACTION){
                    const { accounts } = this.props;
                    let balance = new BigNumber(accounts.selected.balance).shiftedBy(-6).toString();
                    let unit = "TRX";
                    let callValue = 0;
                    let show = false;
                    if(token_id){
                        const tokenArr = (this.tokensMap[token_id] || '').split('_');
                        const decimal = +tokenArr[2] || 0;
                        unit = tokenArr[3] || "";
                        callValue = new BigNumber(call_token_value || 0).shiftedBy(-decimal).toString();
                        show = true;
                        const tokenBalance = lodashGet(accounts, 'selected.tokens.basic.' + token_id +'.balance') || 0;
                        balance = new BigNumber(tokenBalance).shiftedBy(-decimal).toString();
                    } else if(call_value > 0){
                        callValue = new BigNumber(call_value || 0).shiftedBy(-6).toString();
                        show = true
                    }
                    if(show){
                        // Get Address Book
                        // const addressBooks = await PopupAPI.getAddressBooks();
                        let toAddress = TronWeb.address.fromHex(contract_address);
                        toAddress = [toAddress.substring(0, 8), toAddress.substring(toAddress.length - 7 ,toAddress.length)].join('...');
                        let sendAddress = TronWeb.address.fromHex(owner_address);
                        if(sendAddress && sendAddress.length > 10){
                            sendAddress = [sendAddress.substring(0, 5), sendAddress.substr(sendAddress.length - 5 ,sendAddress.length)].join('...');
                        }
                        const name = lodashGet(accounts,'selected.name');
                        if (name) {
                            sendAddress = `${name.length > 7 ? name.substring(0, 5) + '...' : name}(${sendAddress})`;
                        }
                        this.setState({
                            consume: {
                                unit,
                                callValue,
                                balance,
                                toAddress,
                                sendAddress
                            }
                        })
                    }else {
                        this.setState({
                            consume: false
                        })
                    }
                }

                const abi = await PopupAPI.getAbiCode(contract_address);
                const args = Utils.decodeParams(parameter, abi, function_selector);
                this.setState({ args });
            }catch (e) {
                console.log('get abi err:', e.message)
            }
        }
    }

    componentWillUnmount(){
        window.removeEventListener('message',this.listener,false);
    }

    loadWhitelistOptions({ formatMessage }) {
        const options = [{
            value: 60 * 60 * 1000,
            label: formatMessage({ id: 'CONFIRMATIONS.NEW.OPTIONS.ONE_HOUR' })
        }, {
            value: 24 * 60 * 60 * 1000,
            label: formatMessage({ id: 'CONFIRMATIONS.NEW.OPTIONS.ONE_DAY' })
        }, {
            value: -1,
            label: formatMessage({ id: 'CONFIRMATIONS.OPTIONS.NEXT_LOGIN' })
        }, {
            value: -2,
            label: formatMessage({ id: 'CONFIRMATIONS.NEW.OPTIONS.AUTO_AUTHORIZE' })
        }];

        // eslint-disable-next-line
        this.state = {
            args: [],
            showArgs: false,
            whitelisting: {
                selected: options[0],
                options,
                isAutoAuthorize: false
            },
            loading: false,
            loadingTip: false,
            active: false,
            approveShow: false,
            approveShowAmount: true,
            approveParams: {
                address: '',
                amount: '',
                unit: ''
            },
            consume: false,
        };
    }

    async addUsedDapp() {
        const { hostname } = this.props.confirmation;
        const dappList = await PopupAPI.getDappList(true);
        const { used } = dappList;
        const tronDapps = await PopupAPI.getAllDapps();
        const regExp = new RegExp(hostname);
        if (used.length && used.some(({ href }) => href.match(regExp))) {
            const index = used.findIndex(({ href }) => href.match(regExp));
            const item = used.find(({ href }) => href.match(regExp));
            used.splice(index, 1);
            used.unshift(item);
        } else {
            const dapp = tronDapps.filter(({ href }) => href.match(regExp));
            if (dapp.length) used.unshift(dapp[0]);
        }
        dappList.used = used;
        PopupAPI.setDappList(dappList);
    }

    onReject() {
        PopupAPI.rejectConfirmation();
        PopupAPI.setGaEvent('Authorize', 'Authorize Refuse', 'Authorize Refuse')
    }

    async onAccept() {
        const {
            selected,
            isAutoAuthorize
        } = this.state.whitelisting;
        const { confirmation, authorizeDapps } = this.props;
        if (confirmation.contractType === 'TriggerSmartContract') {
            await this.addUsedDapp();
            const hostname = confirmation.hostname;
            if (isAutoAuthorize && selected.value === -2 && !authorizeDapps.hasOwnProperty(hostname)) {
                const o = {};
                o.url = confirmation.hostname;
                o.addTime = new Date().getTime();
                authorizeDapps[hostname] = o;
                PopupAPI.setAuthorizeDapps(authorizeDapps);
            }
        }
        PopupAPI.acceptConfirmation(isAutoAuthorize ? selected.value : false);
        PopupAPI.setGaEvent('Authorize', 'Authorize Accept', 'Authorize Accept')
    }

    async onAcceptLedger() {
        let {
            signedTransaction
        } = this.props.confirmation;
        this.setState({loading: true});
        // if(signedTransaction && typeof signedTransaction === 'string'){ // stringç­¾åå…ˆåšhash,ä¸ºäº†å’ŒtronwebéªŒç­¾å…¼å®¹
        //     signedTransaction = TronWeb.utils.ethersUtils.keccak256(signedTransaction.startsWith('0x') ? signedTransaction : '0x' + signedTransaction)
        //     signedTransaction = signedTransaction.replace('0x', '');
        // }
        const {selected} = this.props.accounts;
        document.querySelector('#tronLedgerBridge').contentWindow.postMessage({target:"LEDGER-IFRAME",action:'sign transaction',data:{transaction: signedTransaction, index: selected.accountIndex, fromAddress: selected.address}},'*');
    }

    handleClose(){
        this.setState({loading:false, loadingTip: false});
    }

    async listener(event){
        const { selected } = this.props.accounts;
        const { formatMessage } = this.props.intl;
        if(event.data.target==='LEDGER-IFRAME'){
            console.log(event.data);
            const {success, error, signedTransaction} = event.data;
            if(success){
                this.setState({loading: false});
                const {
                    selected : select
                } = this.state.whitelisting;
                const { confirmation, authorizeDapps } = this.props;
                if (confirmation.contractType === 'TriggerSmartContract') {
                    await this.addUsedDapp();
                }
                PopupAPI.acceptConfirmation(false, signedTransaction);
            } else {
                let id = '';
                if(error === 'User has not unlocked wallet'){
                    id = 'CREATION.LEDGER.CONNECT_TIMEOUT';
                }else if(error.match(/denied by the user/)){
                    id = 'CREATION.LEDGER.REJECT';
                }else if(error.match(/U2F TIMEOUT/)){
                    id = 'CREATION.LEDGER.AUTHORIZE_TIMEOUT';
                }else if(error === "Cannot read property 'message' of undefined"){
                    id = 'CREATION.LEDGER.NO_TOKEN';
                }else if(error === "address not match"){
                    id = 'CREATION.LEDGER.NOT_MATCH';
                }
                if(error.toString().includes('0x6a8b')){
                    this.setState({loadingTip: 'CREATION.LEDGER.TIP.OPEN_DATA'});
                    return
                } else if(error.toString().includes('0x6a8c')){
                    this.setState({loadingTip: 'CREATION.LEDGER.TIP.OPEN_HASH'});
                    return
                } else if(error.toString().includes('0x6a8d')){
                    this.setState({loadingTip: 'CREATION.LEDGER.TIP.OPEN_CUSTOM'});
                    return
                }
                this.setState({loading: false});
                Toast.fail(id ? formatMessage({id}) : error, 3, () => {
                    PopupAPI.setGaEvent('Ledger','Rejected Transaction',selected.address);
                }, true);
            }
        }
    }

    onWhitelist(selected) {
        this.setState({
            whitelisting: {
                ...this.state.whitelisting,
                selected
            }
        });
    }

    splitFunction(value, isFirst){
        const { showArgs } = this.state;
        let index = 0;
        let valueArr = [];
        while(index < value.length){
            valueArr.push(value.substring(index, index + 40))
            index  = index + 40
        }
        if(isFirst){
            valueArr = [valueArr[0]]
        }else{
            valueArr = valueArr.slice(1)
        }

        return <div style={isFirst ? {} :{ "margin-top": "-10px"}}>
            {
                valueArr.map((clips, index) => {
                    if (index === 0 && isFirst){
                        return <div className="firstFunc"> {value.length > 40 ? clips.substring(0,40) + (showArgs ? '' : '...') : clips} </div>
                    } else {
                        return <div className="func"> {clips} </div>
                    }
                })
            }
        </div>

    }

    async approveParams(input){
        const { args, approveShow } = this.state;
        if(approveShow){
            return
        }
        if(input.function_selector === 'approve(address,uint256)' && input.contract_address){
            let amount = '';
            let unit = '';
            let address = '';
            let approveShowAmount = true
            if(lodashSize(args) >= 2){
                address = lodashGet(args,'0.value');
                amount = lodashGet(args, '1.value');
                const tokenInfo = await PopupAPI.getTokenById(TronWeb.address.fromHex(input.contract_address));
                const decimals = lodashGet(tokenInfo, 'decimals') || 0;
                unit = lodashGet(tokenInfo, 'abbr');
                if(new BigNumber(amount).gte(new BigNumber('0xfffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'))){
                    approveShowAmount = false
                }
                amount = new BigNumber(amount).shiftedBy(-decimals).toString();
                if(unit && address && amount && parseInt(amount) > 0){
                    this.setState({
                        approveShow: true,
                        approveParams: {
                            address,
                            amount,
                            unit
                        },
                        approveShowAmount
                    })
                }
            }
        }
    }

    renderTransaction() {
        const { showArgs, approveShow, approveShowAmount, consume, approveParams } = this.state;
        let { args } = this.state;
        const {
            options,
            selected,
            isAutoAuthorize
        } = this.state.whitelisting;

        const {selected: nodeSelected} = this.props.nodes;

        const {
            formatMessage,
            formatNumber
        } = this.props.intl;

        const {
            contractType,
            input,
            chainType,
            type: confirmType
        } = this.props.confirmation;

        const { selected: { type } } = this.props.accounts;
        if(lodashSize(args) === 0){
            args = lodashGet(this.props.confirmation, 'signedTransaction.raw_data_hex') || []
        }

        const meta = [];
        const showWhitelist = contractType === 'TriggerSmartContract';
        let showParameters = false;
        let netTypeText = 'CONFIRMATIONS.NEW.NETWORK.MAIN_NET';
        switch (nodeSelected) {
            case 'b9424719-b45b-45aa-95d0-1b1b25fc75ae': // shasta
                netTypeText = 'CONFIRMATIONS.NEW.NETWORK.SHASTA_NET';
                break;
            case 'f14212e2-a6a0-4391-9419-07b55f8be63e': // tron ex
            case '01eda3a0-5a58-4e44-9f95-f7f1f59dd728': // tron ex
                netTypeText = 'CONFIRMATIONS.NEW.NETWORK.TRONEX_NET';
                break;
            case '910d7fa5-da35-419d-b454-fd4ee22087cd': // nil ex
                netTypeText = 'CONFIRMATIONS.NEW.NETWORK.NILE_NET';
                break;
        }
        const chainTypeText = Number(chainType) === 1 ? 'CONFIRMATIONS.NEW.NETWORK.DAPPCHAIN' : 'CONFIRMATIONS.NEW.NETWORK.MAINCHAIN';
        meta.push({key: 'CONFIRMATIONS.NEW.TYPE', value: formatMessage({id: `CONTRACTS.${contractType ? contractType : 'SignMessage'}`}) });
        meta.push({key: 'CONFIRMATIONS.NEW.NETWORK', value: `${formatMessage({id: netTypeText})} / ${formatMessage({id: chainTypeText}) }` });

        if(confirmType === CONFIRMATION_TYPE.TRANSACTION){
            if(input.contract_address){
                const address = TronWeb.address.fromHex(input.contract_address);
                const trimmed = [
                    address.substr(0, 10),
                    address.substr(24)
                ].join('...');
                meta.push({ key: 'CONFIRMATIONS.NEW.CONTRACT', value: TronWeb.address.fromHex(address) });
            }

            if (input.function_selector) {
                meta.push({ key: 'CONFIRMATIONS.NEW.FUNCTION', value: input.function_selector });
                // approve cal
                this.approveParams(input)
            }

            if(!(input.contract_address || input.function_selector) && input.amount){
                if (contractType === 'TransferContract') {
                    meta.push({ key: 'CONFIRMATIONS.NEW.QUANTITY', value: formatNumber(input.amount / 1000000, { maximumFractionDigits: 10 }) + ' TRX'  });
                } else {
                    let decimal = 0;
                    const tokenStr = this.tokensMap[TronWeb.toUtf8(input.asset_name)] || '';
                    const tokenArr = tokenStr.split('_');
                    if (input.asset_name && tokenArr.length > 0) {
                        decimal = +tokenArr[2] || 0;
                    }
                    meta.push({ key: 'CONFIRMATIONS.NEW.QUANTITY', value: formatNumber(input.amount / Math.pow(10, decimal), { maximumFractionDigits: 10 }) + ' ' + (tokenArr[3] || '') });
                }
                if (input.to_address) {
                    const address = TronWeb.address.fromHex(input.to_address);
                    const trimmed = [
                        address.substr(0, 10),
                        address.substr(24)
                    ].join('...');
                    meta.push({ key: 'CONFIRMATIONS.NEW.RECIPIENT', value: address });
                }
            }

            switch (contractType) {
                case 'ProposalCreateContract':
                case 'ExchangeCreateContract':
                case 'ExchangeInjectContract':
                case 'ExchangeWithdrawContract':
                case 'CreateSmartContract':
                    showParameters = true;
                    break;
                default:
                    showParameters = false;
            }
        }

        return (
            <React.Fragment>
                <div className="body scroll" style={showWhitelist && type !== 2 && confirmType === CONFIRMATION_TYPE.TRANSACTION? {} : {"height": "440px"}}>
                    {
                        consume ?
                            <div className="consume">
                                <div className="amounts">
                                    {consume.callValue + ' ' + consume.unit}
                                </div>
                                <div className="detail">
                                    <div className="left center-align">
                                        <div className="acronyms center-align">{consume.sendAddress.substring(0, 2)}</div>
                                        <div className="address center-align">{consume.sendAddress}</div>
                                        <div className="balance center-align">{consume.balance || 0} {consume.unit}</div>
                                    </div>
                                    <div className="middle center-align"> </div>
                                    <div className="fight center-align">
                                        <div className="acronyms center-align">{consume.toAddress.substring(0,2)}</div>
                                        <div className="address center-align">{consume.toAddress}</div>
                                        <div className="balance center-align">&nbsp;</div>
                                    </div>
                                </div>
                            </div>
                            : null
                    }

                    {
                        approveShow ?
                            <div className="consume center-align">
                                <div className="title">
                                    <FormattedMessage id={"CONFIRMATIONS.APPROVE_DESC"} values={{
                                        address: lodashSize(approveParams.address) > 5 ? approveParams.address.substring(0, 5) + '...' + approveParams.address.substring(approveParams.address.length - 5) : ''
                                    }}/>
                                    {/*å…è®¸ {lodashSize(approveParams.address) > 5 ? approveParams.address.substring(0, 5) + '...' + approveParams.address.substring(approveParams.address.length - 5) : null} èŠ±è´¹æ‚¨çš„*/}
                                </div>
                                <div className="fee">
                                    {approveShowAmount ? approveParams.amount : ''} {approveParams.unit}
                                    <img src={questionImg} alt="" data-tip={formatMessage({ id: 'CONFIRMATIONS.APPROVE_QUESTION_TIP' })} data-for='showQuestion'/>
                                    <ReactTooltip id='showQuestion' effect='solid' place="bottom"/>
                                </div>
                                <div className="tip">
                                    <FormattedMessage id={"CONFIRMATIONS.APPROVE_TIP"} />
                                </div>
                            </div>
                            : null
                    }

                    {meta.length ? (
                        <div className='meta'>
                            {meta.map(({ key, value }) => (
                                key === 'CONFIRMATIONS.NEW.FUNCTION' ?
                                    <div className={'function' + (showArgs ? ' show' : '')} >
                                        <div data-tip={formatMessage({ id: 'CONFIRMATIONS.CLICK_SHOW_PARAMS' })}
                                             data-for='showArgs' className='metaLine' style={{"border":"none"}}
                                             onClick={() => this.setState({ showArgs: !showArgs })}
                                             key={key}>
                                            <FormattedMessage id={key}/>

                                            {/*<span className='value'>*/}
                                            {/*    {value}*/}
                                            {/* </span>*/}
                                            {
                                                this.splitFunction(value, true)
                                            }
                                            {args.length ? <ReactTooltip id='showArgs' effect='solid'/> : null}
                                        </div>
                                        {
                                            this.splitFunction(value)
                                        }
                                        <div className="show_arg" onClick={e => e.stopPropagation()}>
                                            {
                                                Object.prototype.toString.call(args).slice(8, -1) === 'Array' ?
                                                    JSON.stringify(args.map(({ name, value }) => {
                                                        const v = {};
                                                        v[name] = value;
                                                        return v;
                                                    }))
                                                    : args
                                            }
                                        </div>
                                    </div>
                                    :
                                    <div className='metaLine' key={key}>
                                        <FormattedMessage id={key}/>
                                        <span className='value'>
                                        {value}
                                    </span>
                                    </div>
                            ))}
                        </div>
                    ) : null}
                    {
                        confirmType === CONFIRMATION_TYPE.STRING ?
                            <div className="inputString">
                                {input}
                            </div>
                            : null
                    }
                    {showParameters ? (
                        <div className='inputString mono'>
                            {JSON.stringify(input, null, 2)}
                        </div>
                    ) : null}
                </div>
                {showWhitelist && type !== 2 && confirmType === CONFIRMATION_TYPE.TRANSACTION ? (
                    <div className='whitelist'>
                        <FormattedMessage
                            id='CONFIRMATIONS.WHITELIST.TITLE'
                            children={text => (
                                <div className='whitelistTitle'>
                                    <div>{text}</div>
                                    <div>
                                        <Switch
                                            checked={isAutoAuthorize}
                                            onClick={() => {
                                                let whitelisting = this.state.whitelisting
                                                whitelisting.isAutoAuthorize = !isAutoAuthorize
                                                this.setState({
                                                    whitelisting
                                                });
                                            }}
                                            color="#636ACC"
                                        />
                                    </div>
                                </div>
                            )}
                        />
                        <FormattedMessage
                            id='CONFIRMATIONS.WHITELIST.BODY'
                            children={text => (
                                <div className='whitelistBody'>
                                    {text}
                                </div>
                            )}
                        />
                        <Dropdown
                            disabled={!isAutoAuthorize}
                            className='dropdown'
                            options={options}
                            value={selected}
                            onChange={this.onWhitelist}
                        />
                    </div>
                ) : null}
            </React.Fragment>
        );
    }

    render() {
        const {
            type,
            input: { parameter, contract_address },
            chainType,
            hostname
        } = this.props.confirmation;

        const { loading, loadingTip } = this.state;
        const chainTypeText = Number(chainType) === 1 ? 'DAppChain' : 'MainChain';
        return (
            <div className='insetContainer confirmationController'>
                {
                    <div className='greyModal confirmModal'>
                        <FormattedMessage id='CONFIRMATIONS.NEW.HEADER'
                                          children={text => (
                                              <div className='pageHeader'>
                                                  {text}
                                              </div>
                                          )}
                        />
                        <div className='modalDesc'>
                            <span data-tip={encodeURIComponent(hostname)} data-for='showHostname'>
                                {encodeURIComponent(hostname).length > 40 ? encodeURIComponent(hostname).substring(0, 37) + '...' : encodeURIComponent(hostname)}
                            </span>
                            <ReactTooltip id='showHostname' effect='solid' place="bottom"/>
                            &nbsp;
                            <FormattedHTMLMessage
                                id='CONFIRMATIONS.NEW.BODY'
                                values={{
                                    hostname: ''
                                }}
                            />

                        </div>
                        {
                            this.renderTransaction()
                        }
                        {
                            this.props.type !== ACCOUNT_TYPE.LEDGER
                                ?
                                <div className='buttonRow'>
                                    <Button
                                        id='BUTTON.REJECT'
                                        type={BUTTON_TYPE.DANGER}
                                        onClick={this.onReject}
                                        tabIndex={3}
                                    />
                                    <Button
                                        id='BUTTON.ACCEPT'
                                        onClick={this.onAccept}
                                        tabIndex={2}
                                    />
                                </div>
                                :
                                <div>
                                    <Loading show={loading} tip={loadingTip} onClose={this.handleClose.bind(this)}/>
                                    <div className='buttonRow'>
                                        <Button
                                            id='BUTTON.REJECT'
                                            type={BUTTON_TYPE.DANGER}
                                            onClick={this.onReject}
                                            tabIndex={3}
                                        />
                                        <Button
                                            id='BUTTON.ACCEPT'
                                            onClick={this.onAcceptLedger}
                                            tabIndex={2}
                                        />
                                    </div>
                                </div>
                        }
                    </div>

                }
            </div>
        );
    }
}

export default injectIntl(
    connect(state => ({
        type: state.accounts.selected.type,
        confirmation: state.confirmations[0]
    }))(ConfirmationController)
);
