import React from 'react';
import { FormattedMessage, injectIntl } from 'react-intl';
import { BigNumber } from 'bignumber.js';
import { PopupAPI } from "@tronlink/lib/api";
import Button from '@tronlink/popup/src/components/Button';
import Loading from '@tronlink/popup/src/components/Loading';
import { VALIDATION_STATE, APP_STATE, CONTRACT_ADDRESS, ACCOUNT_TYPE, TOP_TOKEN } from '@tronlink/lib/constants';
import TronWeb from "tronweb";
import { Toast } from 'antd-mobile';
import Utils  from '@tronlink/lib/utils';
import crypto from 'crypto';
import {map as lodashMap, size as lodashSize, get as lodashGet, toString as lodashToString, debounce as lodashDebounce} from 'lodash';
import AddressBookDetail from './AddressBookController/AddressBookDetail';

const addImg = require('@tronlink/popup/src/assets/images/new/icon-add4.svg');
const addAddressBookImg = require('@tronlink/popup/src/assets/images/new/icon_address_book.png');
const trxImg = require('@tronlink/popup/src/assets/images/new/trx.png');
class SendController extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: {
                account: false,
                token: false,
                toAccount: false,
                note: false,
                fee: false,
            },
            selectedToken: {
                id: '_',
                name: 'TRX',
                amount: 0,
                decimals: 6,
                abbr: 'TRX',
                warn: ''
            },
            recipient: {
                error: '',
                warn: '',
                value: '',
                valid: false,
                isActivated: true
            },
            amount: {
                error: '',
                value: '',
                valid: false,
                values: ''
            },
            costs: {
                bandwidth: 0,
                energy: 0,
                createAccount: 0,
            },
            note: '',
            loading: false,
            loadingTip: false,
            loadingLedger: false,
            allTokens:[],
            selectedAddress:'',
            recentAddress: [],
            addressBooks: [],
            showInputAddress:'',
            isNewAddress: false,
            showAddNodeDialog: false,
            localRecord: false,
        };
        this.listener = this.listener.bind(this);
        // this.validateAmount = this.validateAmount.bind(this)
        this.debounceFunction = lodashDebounce(this.validateAmount, 500, { 'maxWait': 3000 }).bind(this)
    }

    listener(event){
        const { selected } = this.props.accounts;
        const { formatMessage } = this.props.intl;
        const { localRecord } = this.state;
        if(event.data.target==='LEDGER-IFRAME'){
            console.log(event.data);
            if(event.data.success){
                this.setState({loading: false,loadingLedger: false, loadingTip: false});
                const txId = lodashGet(event.data, 'signedTransaction.txID');
                const timestamp = lodashGet(event.data, 'signedTransaction.raw_data.timestamp');
                if (localRecord && txId && timestamp) {
                    localRecord.timestamp = timestamp;
                    localRecord.txId = txId;
                    localRecord.local = true;
                    PopupAPI.addRecentRecord(localRecord);
                }
                Toast.success(formatMessage({ id: 'SEND.SUCCESS' }), 3, () => {
                    this.onCancel();
                    PopupAPI.setGaEvent('Ledger','Confirmed Transaction',selected.address);
                }, true);
            } else {
                let id = '';
                if(event.data.error === 'User has not unlocked wallet'){
                    id = 'CREATION.LEDGER.CONNECT_TIMEOUT';
                }else if(event.data.error.match(/denied by the user/)){
                    id = 'CREATION.LEDGER.REJECT';
                }else if(event.data.error.match(/U2F TIMEOUT/)){
                    id = 'CREATION.LEDGER.AUTHORIZE_TIMEOUT';
                }else if(event.data.error === "Cannot read property 'message' of undefined"){
                    id = 'CREATION.LEDGER.NO_TOKEN';
                }else if(event.data.error === "address not match"){
                    id = 'CREATION.LEDGER.NOT_MATCH';
                }
                if(event.data.error.toString().includes('0x6a8b')){
                    this.setState({loadingTip: 'CREATION.LEDGER.TIP.OPEN_DATA'});
                    return
                } else if(event.data.error.toString().includes('0x6a8c')){
                    this.setState({loadingTip: 'CREATION.LEDGER.TIP.OPEN_HASH'});
                    return
                } else if(event.data.error.toString().includes('0x6a8d')){
                    this.setState({loadingTip: 'CREATION.LEDGER.TIP.OPEN_CUSTOM'});
                    return
                }
                this.setState({loadingLedger: false,loading: false, loadingTip: false});
                Toast.fail(id ? formatMessage({id}) : event.data.error, 3, () => {
                    PopupAPI.setGaEvent('Ledger','Rejected Transaction',selected.address);
                }, true);
            }
        }
    }
    async componentDidMount() {
        const allTokens = await PopupAPI.getAllTokens();
        this.setState({allTokens});
        let {selectedToken,selected} = this.props.accounts;
        selectedToken.amount = selectedToken.id === '_' ? selected.balance / Math.pow(10 ,  6) : selectedToken.amount;
        this.setState({selectedToken,selectedAddress:selected.address});
        window.addEventListener('message',this.listener,false);
        const recentRecords = await PopupAPI.getRecentRecord();
        const addressBooks = await PopupAPI.getAddressBooks();
        this.setState({recentAddress: lodashMap(recentRecords, "toAddress"), addressBooks})
        const selectAddressBook = await PopupAPI.getSelectAddressBook();
        if (lodashSize(selectAddressBook) > 0) {
            const {address = '', name = ''} = selectAddressBook;
            let showAddress = address;
            if(name){
                showAddress = `${name}(${address})`;
                if (showAddress.length > 34) {
                    showAddress = `${showAddress.substr(0, 25)}...${showAddress.substr(-6)}`
                }
            }
            this.onRecipientChange(address, showAddress);
        }
        await PopupAPI.setSelectAddressBook({});
        this.calFee()
    }

    componentWillUnmount(){
        window.removeEventListener('message',this.listener,false);
    }

    async componentWillReceiveProps(nextProps) {
        const { selected } = nextProps.accounts;
        const { selectedToken } = this.state;
        const field = selectedToken.id.match(/^T/) ? 'smart':'basic';
        const balance = selected.tokens[field].hasOwnProperty(selectedToken.id) ? (selected.tokens[field][ selectedToken.id ].balanceStr != undefined ? selected.tokens[field][ selectedToken.id ].balanceStr : selected.tokens[field][ selectedToken.id ].balance ): 0;
        const decimals = selected.tokens[field].hasOwnProperty(selectedToken.id) ? selected.tokens[field][ selectedToken.id ].decimals : 6;
        if(selectedToken.id === '_') {
            selectedToken.amount = new BigNumber(selected.balance).shiftedBy(-6).toString();
        } else {
            selectedToken.amount = new BigNumber(balance).shiftedBy(-decimals).toString();
        }
        this.setState({ selectedToken });
        //all token
        const { apiStatus } = nextProps;
        if(apiStatus && apiStatus['allTokenUpdate']){
            const allTokens = await PopupAPI.getAllTokens();
            if(allTokens.length){
                this.setState({allTokens});
                PopupAPI.setApiStatus('allTokenUpdate', false);
            }
        }
        const addressBooks = await PopupAPI.getAddressBooks();
        this.setState({addressBooks})
        this.calFee()
    }

    changeToken(selectedToken,e) {
        e.stopPropagation();
        const { isOpen, recipient, showInputAddress } = this.state;
        const { value } = this.state.amount;
        isOpen.token = !isOpen.token;
        if (selectedToken.id.match(/^T/)) {
            selectedToken.warn = 'WARN.SEND.TRC20_TOKEN_WARN';
        } else {
            selectedToken.warn = '';
        }
        this.setState({ isOpen, selectedToken },() => this.validateAmount());
        PopupAPI.setSelectedToken(selectedToken);
        PopupAPI.setGaEvent('Transfer','Click choose token','Click choose token')
        this.onRecipientChange(recipient.value, showInputAddress);
    }

    async changeAccount(address, e) {
        e.stopPropagation();
        const { isOpen,recipient, showInputAddress } = this.state;
        isOpen.account = !isOpen.account;
        const { selected, accounts } = this.props.accounts;
        const selectedToken = {
            isMapping : true,
            imgUrl: trxImg,
            id: '_',
            name: 'TRX',
            decimals: 6,
            amount: new BigNumber(accounts[ address ].balance).shiftedBy(-6).toString(),
            balance : new BigNumber(accounts[ address ].balance - accounts[ address ].frozenBalance).shiftedBy(-6).toString(),
            frozenBalance : new BigNumber(accounts[ address ].frozenBalance).shiftedBy(-6).toString()
        };

        if(selected.address === address)
            return;

        await PopupAPI.selectAccount(address);
        await PopupAPI.setSelectedToken(selectedToken);
        this.setState({ isOpen, selectedToken, selectedAddress:address },() => { this.validateAmount() });
        await this.onRecipientChange(recipient.value, showInputAddress);
        const addressBooks = await PopupAPI.getAddressBooks();
        this.setState({addressBooks})
        PopupAPI.setGaEvent('Transfer', 'Change Transfer Address', 'Change Transfer Address')
    }

    async changeToAccount(address, showAddress, e){
        e.stopPropagation();
        this.onRecipientChange(address, showAddress);
    }

    async onRecipientChange(address, showAddress) {
        const { selectedAddress, isOpen, addressBooks, costs } = this.state;
        const { chains } = this.props;
        const recipient = {
            value: address,
            valid: VALIDATION_STATE.NONE
        };
        isOpen.toAccount = false;
        this.setState({isOpen, showInputAddress: showAddress || address});
        costs.createAccount = 0;
        if(!address.length)
            return this.setState({recipient:{value: '', valid: false, error: ''}, isNewAddress: false, costs});

        const selectAccount = await PopupAPI.getSelectedAccount()

        if(!TronWeb.isAddress(address)) {
            recipient.valid = false;
            recipient.error = 'EXCEPTION.SEND.ADDRESS_FORMAT_ERROR';
            this.setState({isNewAddress: false})
        } else {
            const {id} = this.state.selectedToken;
            const account = await PopupAPI.getAccountInfo(address);
            if(!account[chains.selected === '_'? 'mainchain' : 'sidechain' ].address) {
                recipient.isActivated = false;
                recipient.valid = true;
                if(id.match(/^T/)){
                    recipient.warn = 'WARN.SEND.ADDRESS_UNACTIVATED_TRC20_WARN';
                    costs.createAccount = 0;
                } else {
                    if(selectAccount.netLimit - 5000 - selectAccount.NetUsedOnly >= 200){
                        recipient.warn = 'WARN.SEND.ADDRESS_UNACTIVATED_NET_WARN';
                        costs.createAccount = 0;
                    } else {
                        recipient.warn = 'WARN.SEND.ADDRESS_UNACTIVATED_WARN';
                        costs.createAccount = 0.1;
                    }
                }
            } else if(address === selectedAddress) {
                recipient.isActivated = true;
                recipient.valid = false;
                recipient.error = 'EXCEPTION.SEND.ADDRESS_SAME_ERROR';
            } else {
                recipient.isActivated = true;
                recipient.valid = true;
                recipient.error = '';
                recipient.warn = '';
            }
            if (!addressBooks[address]) {
                this.setState({isNewAddress: true})
            } else {
                this.setState({isNewAddress: false})
            }
        }
        this.setState({
            recipient,
            costs
        },() => this.validateAmount());
    }

    onAmountChange(e) {
        const amount = e.target.value;
        this.setState({
            amount: {
                value: amount,
                valid: false
            }
        }

           ,() => this.validateAmount()
        );
    }

    calFee(){
        const {
            id
        } = this.state.selectedToken;
        const { selected } = this.props.accounts;
        let { costs, note } = this.state;
        // Calculation of trx for bandwidth consumption
        let baseBandwidth = 267; //trx
        if(id.match(/^T/)){
            baseBandwidth = 344; //trc20
        }else if(id !== '_'){
            baseBandwidth = 281; //trc10
        }
        //Calculating the bandwidth consumed by the note
        let noteCost = 0;
        if(note){
            // noteCost = new BigNumber(lodashSize(Buffer.from(lodashToString(note), 'utf8').toString('hex'))).dividedBy(2).plus(2).toNumber();
            noteCost = new BigNumber(Buffer.byteLength(lodashToString(note), 'utf8')).plus(2).toNumber();
        }

        let consume = new BigNumber(0);
        if(selected.netLimit - selected.netUsed - baseBandwidth - noteCost < 0){
            consume = new BigNumber(baseBandwidth).plus(noteCost).multipliedBy(10).shiftedBy(-6);
        }
        costs.bandwidth = consume.toNumber();
        // console.log("baseBandwidth:", baseBandwidth, noteCost, costs.bandwidth )
        this.setState({costs});
    }

    validateAmount() {
        const {
            amount:tokenCount,
            decimals,
            id
        } = this.state.selectedToken;
        const { selected } = this.props.accounts;
        let { amount, costs, note } = this.state;

        this.calFee()
        const consume = costs.bandwidth || 0;

        let error = '';
        if(new BigNumber(selected.balance).shiftedBy(-6).lt(new BigNumber(costs.createAccount || 0).plus(consume)) ){
            error = 'ACCOUNT.TRANSFER.WARNING.TRX_NOT_ENOUGH';
        }
        if(amount.value === '') {
            return this.setState({
                amount: {
                    valid: false,
                    value: '',
                    error: error
                }
            });
        }

        const value = new BigNumber(amount.value);
        if(value.isNaN() || value.lte(0)) {
            return this.setState({
                amount: {
                    ...amount,
                    valid: false,
                    error: 'EXCEPTION.SEND.AMOUNT_FORMAT_ERROR'
                }
            });
        }else if(value.gt(tokenCount)) {
            return this.setState({
                amount: {
                    ...amount,
                    valid: false,
                    error: 'EXCEPTION.SEND.AMOUNT_NOT_ENOUGH_ERROR'
                }
            });
        }else if(value.dp() > decimals) {
            return this.setState({
                amount: {
                    ...amount,
                    valid: false,
                    error: 'EXCEPTION.SEND.AMOUNT_DECIMALS_ERROR',
                    values: { decimals: ( decimals === 0 ? '' : '0.' + Array.from({ length: decimals - 1 }, v => 0).join('')) + '1' }
                }
            });
        } else {
            if(!this.state.recipient.isActivated) {
                if(!id.match(/^T/)
                    && (id === '_' && value.gt(new BigNumber(selected.balance).shiftedBy(-6).minus(costs.createAccount || 0).minus(consume)))
                    || (id !=='_' && new BigNumber(selected.balance).shiftedBy(-6).lt(new BigNumber(costs.createAccount || 0).plus(consume))) ) {
                    return this.setState({
                        amount: {
                            ...amount,
                            valid: false,
                            error: 'ACCOUNT.TRANSFER.WARNING.TRX_NOT_ENOUGH'
                        }
                    });
                }
            }else{
                if(id === '_' && value.gt(new BigNumber(selected.balance).shiftedBy(-6).minus(consume))){
                    return this.setState({
                        amount: {
                            ...amount,
                            valid: false,
                            error: 'ACCOUNT.TRANSFER.WARNING.TRX_NOT_ENOUGH'
                            // error: 'EXCEPTION.SEND.BANDWIDTH_NOT_ENOUGH_TRX_ERROR'
                        }
                    });
                }
            }
            if(id.match(/^T/)) {
                const valid = this.state.recipient.isActivated ? true : false;
                if(valid) {
                    // const isEnough = new BigNumber(selected.balance).shiftedBy(-6).gte(new BigNumber(1))   ? true : false;
                    if(new BigNumber(selected.balance).shiftedBy(-6).lt(new BigNumber(costs.createAccount || 0).plus(consume)) ){
                        return this.setState({
                            amount: {
                                ...amount,
                                valid: false,
                                error: 'ACCOUNT.TRANSFER.WARNING.TRX_NOT_ENOUGH'
                                // error: 'EXCEPTION.SEND.BANDWIDTH_NOT_ENOUGH_ERROR'
                            }
                        });
                    }  else {
                        return this.setState({
                            amount: {
                                ...amount,
                                valid: true,
                                error: ''
                            }
                        });
                    }
                } else {
                    return this.setState({
                        amount: {
                            ...amount,
                            //valid,
                            valid:true,
                            //error: 'EXCEPTION.SEND.ADDRESS_UNACTIVATED_TRC20_ERROR',
                            error:''
                        }
                    });
                }
            } else {
                if( new BigNumber(selected.balance).shiftedBy(-6).lt(new BigNumber(costs.createAccount || 0).plus(consume)) ){
                    return this.setState({
                        amount: {
                            ...amount,
                            // valid: id === '_' ? value.lte(new BigNumber(selected.balance).shiftedBy(-6).minus(1)) : new BigNumber(selected.balance).shiftedBy(-6).gte(new BigNumber(1)),   //new BigNumber(selected.balance).shiftedBy(-6).gte(new BigNumber(1)) ? true : false,
                            valid: false,
                            error: 'ACCOUNT.TRANSFER.WARNING.TRX_NOT_ENOUGH'
                        }
                    });
                } else {
                    return this.setState({
                        amount: {
                            ...amount,
                            valid: true,
                            error: ''
                        }
                    });
                }

            }
            return this.setState({
                amount: {
                    ...amount,
                    valid: true,
                    error: ''
                }
            });
        }
    }

    onSend() {
        BigNumber.config({ EXPONENTIAL_AT: [-20,30] });
        this.setState({
            loading: true,
            success: false
        });
        const { selectedToken, selected } = this.props.accounts;
        const { formatMessage } = this.props.intl;
        const { value: recipient } = this.state.recipient;
        const { value: amount } = this.state.amount;
        const { note } = this.state;

        const {
            id,
            decimals,
            name
        } = this.state.selectedToken;
        PopupAPI.setGaEvent('Transfer', 'Click Send', `${name || ''}(${id})`, '', amount);
        if(selected.type !== ACCOUNT_TYPE.LEDGER) {
            let func;
            if (id === "_") {
                func = PopupAPI.sendTrx(
                    recipient,
                    new BigNumber(amount).shiftedBy(6).toString(),
                    note
                );
            } else if (id.match(/^T/)) {
                func = PopupAPI.sendSmartToken(
                    recipient,
                    new BigNumber(amount).shiftedBy(decimals).toString(),
                    id,
                    note
                );
            } else {
                func = PopupAPI.sendBasicToken(
                    recipient,
                    new BigNumber(amount).shiftedBy(decimals).toString(),
                    id,
                    note
                );
            }
            func.then((res) => {
                this.setState({loading: false});
                Toast.success(formatMessage({ id: 'SEND.SUCCESS' }), 3, () => this.onCancel(), true);
                PopupAPI.setGaEvent('Transfer', 'Transfer Success', `Transfer Success`);
                const txId = lodashGet(res, 'txID');
                const timestamp = lodashGet(res, 'raw_data.timestamp');
                if(txId && timestamp){
                    PopupAPI.addRecentRecord({
                        local: true,
                        fromAddress: selected.address,
                        toAddress: recipient,
                        tokenId: id,
                        timestamp: timestamp,
                        txId: txId,
                        decimals,
                        amount
                    })
                }
                // PopupAPI.setPushMessage({
                //     title:`-${amount}${selectedToken.abbr} ${formatMessage({id:'NOTIFICATIONS.TITLE'})}`,
                //     message:formatMessage({id:'NOTIFICATIONS.MESSAGE'}),
                //     hash:res
                // });
            }).catch(error => {
                PopupAPI.setGaEvent('Transfer', 'Transfer Fail', `Transfer Fail`);
                Toast.fail(JSON.stringify(error), 3, () => {
                    this.setState({
                        loading: false
                    });
                }, true);
            });
        } else {
            const iframe = document.querySelector('#tronLedgerBridge').contentWindow;
            const fromAddress = selected.address;
            const toAddress = recipient;
            this.setState({loadingLedger:true});
            if (id === "_") {
                iframe.postMessage({target:"LEDGER-IFRAME",action:'send trx',data:{toAddress,fromAddress,amount:new BigNumber(amount).shiftedBy(6).toString(),index: selected.accountIndex}},'*')
            }else if(id.match(/^T/)){
                iframe.postMessage({target:"LEDGER-IFRAME",action:'send trc20',data:{id,toAddress,fromAddress,amount:new BigNumber(amount).shiftedBy(decimals).toString(),decimals,TokenName:name,index: selected.accountIndex}},'*')
            }else{
                iframe.postMessage({target:"LEDGER-IFRAME",action:'send trc10',data:{id,toAddress,fromAddress,amount:new BigNumber(amount).shiftedBy(decimals).toString(),index: selected.accountIndex}},'*')
            }
            this.setState({
                localRecord: {
                    fromAddress,
                    toAddress,
                    decimals,
                    amount,
                    tokenId: id,
                }
            });
        }
    }

    onCancel() {
        const { selected, selectedToken } = this.props.accounts;
        const token10DefaultImg = require('@tronlink/popup/src/assets/images/new/token_10_default.png');
        if( selected.dealCurrencyPage == 1) {
            const selectedCurrency = {
                id: selectedToken.id,
                name: selectedToken.name,
                abbr: selectedToken.abbr || selectedToken.symbol,
                decimals: selectedToken.decimals,
                amount: selectedToken.amount,
                price: selectedToken.price,
                imgUrl: selectedToken.imgUrl ? selectedToken.imgUrl : token10DefaultImg,
                balance: selectedToken.balance || 0,
                frozenBalance: selectedToken.frozenBalance || 0,
                isMapping : selectedToken.isMapping
            };
            PopupAPI.setSelectedToken(selectedCurrency);
            PopupAPI.changeState(APP_STATE.TRANSACTIONS);
            PopupAPI.changeDealCurrencyPage(0);
        }else {
            PopupAPI.changeState(APP_STATE.READY);
        }
    }

    handleClose(){
        const { formatMessage } = this.props.intl;
        const iframe = document.querySelector('#tronLedgerBridge').contentWindow;
        iframe.postMessage({target:"LEDGER-IFRAME",action:'cancel transaction',data:{}},'*');
        this.setState({loadingLedger:false,loading:false, loadingTip: false},()=>{
            Toast.fail(formatMessage({id:'CREATION.LEDGER.TIP_CANCEL_TRANSACTION'}),3,()=>{},true);
        });
    }

    onConfirmed(name, address) {
        const {formatMessage} = this.props.intl;
        let showAddress = address;
        showAddress = `${name}(${address})`;
        if (showAddress.length > 34) {
            showAddress = `${showAddress.substr(0, 25)}...${showAddress.substr(-6)}`
        }
        this.setState({showAddNodeDialog: false, isNewAddress: false, showInputAddress: showAddress});

        Toast.info(formatMessage({id: 'TOAST.ADDED'}));
    }

    onClose() {
        this.setState({showAddNodeDialog: false});
    }

    render() {
        const {formatMessage} = this.props.intl;
        const {chains, resource} = this.props;
        const topToken = lodashGet(resource, 'topToken') || {};
        const { isOpen, selectedToken, loading, amount, recipient, loadingLedger, loadingTip ,allTokens,
            showInputAddress, recentAddress, addressBooks, isNewAddress, showAddNodeDialog, note, costs } = this.state;
        const { selected, accounts } = this.props.accounts;
        const trx = { tokenId: '_', name: 'TRX', balance: selected.balance,frozenBalance: selected.frozenBalance, abbr: 'TRX', decimals: 6, imgUrl: trxImg,isMapping:true };
        let tokens = { ...selected.tokens.basic, ...selected.tokens.smart};
        const topArray = [];
        const selectTopToken = topToken[chains.selected === '_' ? 'mainchain':'sidechain'];
        if(allTokens.length && selectTopToken && Object.prototype.toString.call(selectTopToken).slice(8, -1) === 'Array') {
            selectTopToken.forEach(v=>{
                const filterTokenInfo = allTokens.filter(({tokenId})=> tokenId === v);
                if(tokens.hasOwnProperty(v)){
                    if(v === CONTRACT_ADDRESS.USDT){
                        // const f = allTokens.filter(({tokenId})=> tokenId === v);
                        tokens[v].imgUrl = filterTokenInfo.length ? filterTokenInfo[0].imgUrl : false;
                    }
                    topArray.push({...tokens[v],tokenId:v});
                }else{
                    filterTokenInfo.length && topArray.push({...filterTokenInfo[0],tokenId:v,price:'0',balance:'0',isLocked:false})
                }
            });
        }
        tokens = Utils.dataLetterSort(Object.entries(tokens).filter(([tokenId, token]) => typeof token === 'object' && !token.hasOwnProperty('chain') || token.chain === chains.selected ).map(v => { v[1].isMapping = v[1].hasOwnProperty('isMapping')?v[1].isMapping:true;v[ 1 ].tokenId = v[ 0 ];return v[ 1 ]; }), 'abbr' ,'symbol',topArray);
        tokens = [trx, ...tokens];
        return (
            <div className='insetContainer send' style={{"overflow": "auto"}} onClick={() =>{
                isOpen.account = false;
                isOpen.token = false;
                isOpen.toAccount = false;
                this.setState({ isOpen })
            } }>
                <Loading show={loadingLedger} tip={loadingTip} onClose={this.handleClose.bind(this)} />
                {
                    showAddNodeDialog ?
                        <AddressBookDetail  show={showAddNodeDialog} onClose={this.onClose.bind(this)}
                                           onConfirmed={this.onConfirmed.bind(this)} address={recipient.value}/>
                        :
                        null
                }
                <div className='pageHeader'>
                    <div className='back' onClick={(e) => this.onCancel() }>&nbsp;</div>
                    <FormattedMessage id='ACCOUNT.SEND' />
                </div>
                <div className='greyModal scroll sendModal'>
                    <div className='input-group'>
                        <label><FormattedMessage id='ACCOUNT.SEND.PAY_ACCOUNT'/></label>
                        <div className={'input dropDown' + (isOpen.account ? ' isOpen' : '')} onClick={ (e) => { e.stopPropagation();isOpen.token = false;isOpen.toAccount=false ;isOpen.account = !isOpen.account; this.setState({ isOpen }); } }>
                            <div className='selected'>{ selected.address }</div>
                            <div className='dropWrap scroll' style={isOpen.account ? (Object.entries(accounts).length <= 5 ? { height : 36 * Object.entries(accounts).length } : { height: 180, overflow: 'scroll'}) : {}}>
                                {
                                    Object.entries(accounts).map(([address]) => <div onClick={(e) => this.changeAccount(address, e) } className={'dropItem'+(address === selected.address?" selected":"")}>{address}</div>)
                                }
                            </div>
                        </div>
                        <div className='otherInfo'>
                            <FormattedMessage id='COMMON.BALANCE'/>:&nbsp;
                            {selected.balance / Math.pow(10, 6)} TRX
                        </div>
                    </div>
                    <div className={'input-group showIcon' + (recipient.error ? ' error' : '')}>
                        <label><FormattedMessage id='ACCOUNT.SEND.RECEIVE_ADDRESS' /></label>
                        <div className={'input dropDown2' + (isOpen.toAccount ? ' isOpen' : '')} style={{"padding":"0 0"}} onClick={ (e) => {
                            e.stopPropagation();
                            if (showInputAddress) {
                                isOpen.toAccount = false;
                            } else {
                                isOpen.account = false;
                                isOpen.token = false;
                                isOpen.toAccount = !isOpen.toAccount;
                            }
                            this.setState({isOpen});
                        }}>
                            <div className='input'>
                                <input type='text' value={showInputAddress} onChange={(e) => this.onRecipientChange(e.target.value) }/>
                            </div>
                            <div className='dropWrap scroll' style={isOpen.toAccount ? (recentAddress.length <= 5 ? { height : 36 * (recentAddress.length + 1) } : { height: 180, overflow: 'scroll'}) : {}}>
                                <div className='dropItem selected'><FormattedMessage id="ACCOUNT.SEND.RECENT_ADDRESS"/></div>
                                {
                                    recentAddress.map((address) => {
                                        let showAddress = address;
                                        if (addressBooks[address]) {
                                            const name = addressBooks[address].name;
                                            showAddress = `${name}(${address})`;
                                            if (showAddress.length > 34) {
                                                showAddress = `${showAddress.substr(0, 25)}...${showAddress.substr(-6)}`
                                            }
                                        }
                                        return <div onClick={(e) => this.changeToAccount(address, showAddress, e)}
                                                    className={'dropItem' + (address === recipient.value ? " selected" : "")}>{showAddress}</div>
                                    })
                                }
                            </div>
                        </div>
                        {
                            recipient.error ?
                                <div className="tipError">
                                    <FormattedMessage id={recipient.error} />
                                </div> :
                                (recipient.warn ?
                                    <div className='tipWarn'>
                                        <FormattedMessage id={recipient.warn} />
                                    </div>
                                    : null
                                )
                        }
                        {/*<div className='tipWarn'>*/}
                        {/*    {recipient.error ? <FormattedMessage id={recipient.error} /> : null}*/}
                        {/*</div>*/}
                        {
                            isNewAddress?
                                <div className="tipAddAddress">
                                    <button className="add" onClick={(e) => {
                                        this.setState({showAddNodeDialog: true})
                                    }}>
                                        <img src={addAddressBookImg} alt="address"/>
                                        <FormattedMessage id='ACCOUNT.SEND.ADD_ADDRESS_BOOK'/>
                                    </button>
                                </div>
                                :
                                null
                        }

                        <span className='span' onClick={async ()=>{
                            await PopupAPI.changeSendToAddress(true);
                            PopupAPI.changeState(APP_STATE.ADDRESS_BOOK);
                        }}>&nbsp;</span>
                    </div>
                    <div className='input-group'>
                        <label><FormattedMessage id='ACCOUNT.SEND.CHOOSE_TOKEN'/></label>
                        <div className={'input dropDown' + (isOpen.token ? ' isOpen' : '')} onClick={ (e) => { e.stopPropagation();isOpen.account = false;isOpen.toAccount=false; isOpen.token = !isOpen.token; this.setState({ isOpen }); } }>
                            <div className='selected'>
                                <span title={`${selectedToken.name}(${selectedToken.amount})`}>{`${selectedToken.name}(${selectedToken.amount})`}</span>{selectedToken.id !== '_' ? (<span>id:{selectedToken.id.length === 7 ? selectedToken.id : selectedToken.id.substr(0, 6) + '...' + selectedToken.id.substr(-6)}</span>) : ''}</div>
                            <div className='dropWrap scroll' style={isOpen.token ? (tokens.length <= 5 ? { height: 36 * tokens.length } : { height: 180, overflow: 'scroll' }) : {}}>
                                {
                                    tokens.filter(({ isLocked = false }) => !isLocked ).map(({ tokenId: id, balance, balanceStr, name, decimals, decimal = false, abbr = false, symbol = false, imgUrl = false,frozenBalance = 0 }) => {
                                        const d =  decimal || decimals;
                                        balance = balanceStr != undefined ? balanceStr : balance;
                                        const BN = BigNumber.clone({
                                            DECIMAL_PLACES: d,
                                            ROUNDING_MODE: Math.min(8, d)
                                        });
                                        let amount = new BN(balance)
                                            .shiftedBy(-d)
                                            .toString();
                                        const frozenAmount = new BN(frozenBalance)
                                            .shiftedBy(-d)
                                            .toString();
                                        const token = { id, amount, name, decimals:d, abbr: abbr || symbol,imgUrl};
                                        return <div onClick={(e) => this.changeToken(id === '_'? {...token, balance:amount, frozenBalance:frozenAmount}:token, e) } className={'dropItem' + (id === selectedToken.id ? ' selected' : '')}><span title={`${name}(${amount})`}>{`${name}(${amount})`}</span>{id !== '_' ? (<span>id:{id.length === 7 ? id : id.substr(0, 6) + '...' + id.substr(-6)}</span>) : ''}</div>

                                    })
                                }
                            </div>
                        </div>
                        {
                            selectedToken.warn ?
                                <div className='tipWarn'>
                                    <FormattedMessage id={selectedToken.warn} />
                                </div>
                                : null
                        }
                    </div>
                    <div className={'input-group hasBottomMargin' + (amount.error ? ' error' : '')}>
                        <label><FormattedMessage id='ACCOUNT.SEND.TRANSFER_AMOUNT' /></label>
                        <div className='input'>
                            <input type='text' value={amount.value} onChange={ (e) => {
                                if(e.target.value != selectedToken.amount){
                                    this.refs['max'].classList.remove('selected');
                                }else{
                                    this.refs['max'].classList.add('selected');
                                }
                                this.onAmountChange(e);
                            }}/>
                            <button className='max' ref='max' onClick={(e)=> {
                                e.target.classList.add('selected');
                                PopupAPI.setGaEvent('Transfer','Click max','Click max')
                                let {amount, id} = selectedToken;
                                if(id === '_'){
                                    amount = new BigNumber(amount).minus(costs.bandwidth).minus(costs.createAccount || 0).minus(costs.energy || 0).toString();
                                    amount  = amount > 0 ? amount : 0
                                }
                                this.setState({
                                        amount: {
                                            value: amount,
                                            valid: false,
                                            error:''
                                        }
                                    }, () => this.validateAmount()
                                );
                            }}>MAX</button>
                        </div>
                        <div className='tipError'>
                            {amount.error ? (amount.values ? <FormattedMessage id={amount.error} values={amount.values} /> : <FormattedMessage id={amount.error} />) : null}
                        </div>
                    </div>
                    <div className="input-group" style={{"margin-bottom": "0px"}}>
                        <div className={"fee" + ((amount.error && selectedToken.id === '_') || amount.error === 'ACCOUNT.TRANSFER.WARNING.TRX_NOT_ENOUGH' ? ' feeError' : '')}>
                            <div className={"aggregate " + (isOpen.fee && (costs.createAccount || costs.bandwidth) ? "  hasBorder": " ")} onClick={ (e) => {
                                e.stopPropagation();
                                if(costs.createAccount || costs.bandwidth){
                                    isOpen.account = false;
                                    isOpen.toAccount = false;
                                    isOpen.fee = !isOpen.fee;
                                    isOpen.token = false;
                                    this.setState({ isOpen });
                                }
                            } }>
                                <span>
                                    <FormattedMessage id="ACCOUNT.SEND.FEE"/>
                                </span>
                                <span className={"icon" + (costs.createAccount || costs.bandwidth ? ' iconAfter' : '') + (isOpen.fee ? ' isOpen' : '')}>{new BigNumber(costs.createAccount||0).plus(costs.bandwidth||0).plus(costs.energy||0).toNumber()} TRX</span>
                            </div>
                            <div className={isOpen.fee ? ' open' : ' close'}>
                                {
                                    costs.createAccount ?
                                        <div className="detail">
                                            <span>
                                                <FormattedMessage id="ACCOUNT.SEND.ACTIVATE_ACCOUNT"/>
                                            </span>
                                            <span>{costs.createAccount} TRX</span>
                                        </div>
                                        : null
                                }
                                {
                                    costs.bandwidth ?
                                        <div className="detail">
                                            <span>
                                                <FormattedMessage id="ACCOUNT.SEND.BANDWIDTH_NOT_ENOUGH"/>
                                            </span>
                                            <span>{costs.bandwidth} TRX</span>
                                        </div>
                                        : null
                                }
                            </div>
                        </div>
                    </div>
                    <div className="input-group">
                        <div className='note'>
                            {
                                isOpen.note
                                    ?
                                    <div className="show" style={{"position": "relative"} }  >
                                        <div className="label">
                                            <label>
                                                <FormattedMessage id='ACCOUNT.SEND.NOTE'/>
                                            </label>
                                            <button onClick={(e) => {
                                                e.stopPropagation();
                                                isOpen.note = false;
                                                this.setState({isOpen: isOpen, note: ""})
                                            }}>
                                                <FormattedMessage id="ACCOUNT.SEND.NOTE_DELETE" />
                                            </button>
                                        </div>
                                        <div>
                                            <textarea className="textarea scroll" placeholder={formatMessage({ id: 'ACCOUNT.SEND.NOTE_DESC' })}
                                                      rows="3" value={note}
                                                      onChange={(e) => {
                                                          e.stopPropagation();
                                                          if(e.target.value.length <= 200){
                                                              this.setState({ note: e.target.value });
                                                              this.debounceFunction()
                                                          }
                                                      }}
                                            />
                                        </div>
                                    </div>
                                    :
                                    <div>
                                        <button className="add" onClick={(e) => {
                                            e.stopPropagation();
                                            isOpen.note = true;
                                            this.setState({isOpen: isOpen})
                                        }}>
                                            <img src={addImg} alt="add"/>
                                            <FormattedMessage id='ACCOUNT.SEND.ADD_NOTE'/>
                                        </button>
                                    </div>
                            }
                        </div>
                    </div>
                    <Button
                        id='ACCOUNT.SEND'
                        isLoading={ loading }
                        type="fixed"
                        isValid={
                            amount.valid &&
                            recipient.valid
                        }
                        onClick={ () => this.onSend() }
                    />

                </div>
            </div>
        );
    }
}

export default injectIntl(SendController);
