import React from 'react';
import {FormattedMessage, injectIntl} from 'react-intl';
import AddressBookDetail from './AddressBookDetail';
import {PopupAPI} from '@tronlink/lib/api';
import {Toast} from 'antd-mobile';
import {values as lodashValues, filter as lodashFilter, orderBy as lodashOrderBy} from 'lodash';

import './AddressBookController.scss';
import TronWeb from "tronweb";
import {APP_STATE} from "@tronlink/lib/constants";

class AddressBookController extends React.Component {
    constructor(props) {
        super(props);
        this.onSearchAddressBooks = this.onSearchAddressBooks.bind(this);
        this.state = {
            showAddNodeDialog: false,
            searchValue: '',
            filterAddressBooks: [],
            isSelect: false
        }
    }

    async componentDidMount() {
        // const {isSelect = false} = this.props;
        const addressBooks = await PopupAPI.getAddressBooks();
        const isSelect = await PopupAPI.changeSendToAddress(false);
        this.setState({filterAddressBooks: lodashValues(addressBooks), isSelect});
        await PopupAPI.setSelectAddressBook({});
    }

    async componentWillReceiveProps(nextProps) {
        const {searchValue} = this.state;
        this.onSearchAddressBooks(searchValue);
    }

    async onSearchAddressBooks(value) {
        // const value = e.target.value;
        const addressBooks = await PopupAPI.getAddressBooks();
        if (value !== '') {
            let filterAddressBooks = [];
            if (TronWeb.isAddress(value)) {
                const bookInfo = addressBooks[value];
                if (bookInfo) {
                    filterAddressBooks.push(bookInfo);
                }
            } else {
                const reg = new RegExp(value);
                filterAddressBooks = lodashFilter(lodashValues(addressBooks), function (bookInfo) {
                    return bookInfo && bookInfo.name.match(reg);
                });
            }
            this.setState({filterAddressBooks});
        } else {
            this.setState({filterAddressBooks: lodashValues(addressBooks)});
        }
        this.setState({searchValue: value})
    }

    onConfirmed() {
        const {formatMessage} = this.props.intl;
        this.setState({showAddNodeDialog: false});
        Toast.info(formatMessage({id: 'TOAST.ADDED'}));
    }

    onClose() {
        this.setState({showAddNodeDialog: false});
    }

    render() {
        const {filterAddressBooks, searchValue, showAddNodeDialog, isSelect} = this.state;
        const {onCancel, onBack} = this.props;
        const {formatMessage} = this.props.intl;
        return (
            <div className='insetContainer addressBook asset'>
                <AddressBookDetail show={showAddNodeDialog} onClose={this.onClose.bind(this)}
                                   onConfirmed={this.onConfirmed.bind(this)}/>
                {
                    isSelect ?
                        <div className='pageHeader'>
                            <div className='back' onClick={() => onBack()}> </div>
                            <FormattedMessage id='SETTING.TITLE.ADDRESS_BOOK.SELECT'/>
                        </div>
                        :
                        <div className='pageHeader'>
                            <div className='back' onClick={() => onCancel()}>&nbsp;</div>
                            <FormattedMessage id='SETTING.TITLE.ADDRESS_BOOK'/>
                            <div className="addItem" onClick={() => this.setState({showAddNodeDialog: true})}> </div>
                        </div>
                }

                <div className='greyModal'>
                    <div className={'input' + (searchValue === '' ? '' : ' hasValue')}>
                        <input type='text' onChange={(e) => {
                            this.onSearchAddressBooks(e.target.value)
                        }} placeholder={formatMessage({id: 'SETTING.TITLE.ADDRESS_BOOK.SEARCH'})}/>
                    </div>
                    <div className='book scroll'>
                        {
                            filterAddressBooks.length ?
                                lodashOrderBy(filterAddressBooks, ['ts'], ['desc']).map((bookInfo) => {
                                    const {address = '', name, remarks = ''} = bookInfo;
                                    let index = 0;
                                    if (searchValue) {
                                        index = (name.match(searchValue) || '')['index'] || 0;
                                    }
                                    return (
                                        <div className='item'>
                                            <div className='r2' onClick={async () => {
                                                Toast.loading('', 0);
                                                await PopupAPI.setSelectAddressBook(bookInfo);
                                                Toast.hide();
                                                if (isSelect) {
                                                    onBack();
                                                } else {
                                                    PopupAPI.changeState(APP_STATE.ADDRESS_BOOK_DETAIL);
                                                }
                                            }}>
                                                <div className='addressName'>
                                                    {
                                                        searchValue && searchValue !== address ?
                                                            <span>{name.substring(0, index)}
                                                                <span
                                                                    className="search">{name.substring(index, index + searchValue.length)}</span>
                                                                {name.substring(index + searchValue.length)}
                                                        </span>
                                                            :
                                                            <span>{name}</span>
                                                    }
                                                </div>
                                                <div className='address'>
                                                    {
                                                        searchValue === address ?
                                                            <span className='search'>{address}</span>
                                                            :
                                                            <span>{address}</span>
                                                    }
                                                </div>
                                                {
                                                    remarks ? <div className='remarks'>
                                                        <FormattedMessage id="SETTING.TITLE.ADDRESS_BOOK.DESC"/>
                                                        {'ï¼š' + remarks}
                                                    </div> : null
                                                }
                                            </div>
                                        </div>
                                    )
                                })
                                :
                                <span className="blankSearch">
                                    {formatMessage({id: 'SETTING.TITLE.ADDRESS_BOOK.SEARCH_BLANK'})}
                                </span>
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default injectIntl(AddressBookController);
